import { get, omitBy, isEqual } from 'lodash-es'
import flatten, { unflatten } from 'flat'

import { humanizeErrorCode } from '../../../utils/humanizeErrors'
import { ERROR_MESSAGES } from './constants'

/*
  Returns error message for field if it's present
  AND (this field was touched OR form was submitted at least one)
*/
export const getErrorMessage = (fieldName, form) => {
  const errorMessage = get(form, `errors.${fieldName}`)
  const wasTouched = get(form, `touched.${fieldName}`)

  if (wasTouched || form.submitCount > 0) {
    return errorMessage
  }
}

export const formatFormErrorMessage = (error, errorMap) => {
  if (!error.code && error.messages) {
    return error.messages[0]
  }

  return get(
    { ...ERROR_MESSAGES, ...errorMap },
    error.code,
    humanizeErrorCode(error.code) || ERROR_MESSAGES.ERR_INTERNAL
  )
}

export const omitNotFunctions = obj => omitBy(obj, something => typeof something !== 'function')

export const getShallowDiff = (initialValues = {}, changedValues = {}) => {
  const initial = flatten(initialValues)
  const changed = flatten(changedValues)

  const diff = {}

  Object.entries(changed).forEach(([key, changedValue]) => {
    if (!isEqual(initial[key], changedValue)) {
      diff[key] = changedValue
    }
  })

  return {
    hasChanges: Object.keys(diff).length > 0,
    diff: unflatten(diff),
  }
}
