import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { sendPageVisit } from './utils'

// eslint-disable-next-line react/display-name
export const withPageviewEvent = (additionalFields = {}) => Component => props => {
  useEffect(() => {
    sendPageVisit(undefined, additionalFields)
  }, [])

  return <Component {...props} />
}

let pageViewsTracked = false

export const withReactRouterPageViewsTracking = WrappedComponent => {
  return class WithReactRouter4PageViewsTracking extends React.Component {
    static propTypes = {
      location: PropTypes.object.isRequired,
    }

    componentDidMount() {
      if (!pageViewsTracked) {
        pageViewsTracked = true
        this.trackPageView()
      } else {
        /* eslint-disable-next-line no-console */
        console.error(
          'WithReactRouter4PageViewsTracking HOC is already in use. You should use it once on the root component.'
        )
      }
    }

    componentDidUpdate(prevProps) {
      if (this.props.location !== prevProps.location) {
        this.trackPageView()
      }
    }

    componentWillUnmount() {
      pageViewsTracked = false
    }

    trackPageView = () => {
      if (this.props.location) {
        sendPageVisit(this.props.location.pathname)
      }
    }

    render() {
      return <WrappedComponent {...this.props} />
    }
  }
}
