import { connect } from 'react-redux'
import Popup from '../../components/elements/Popup'
import { actions, selectors } from './redux'

/**
 * @deprecated Use ModalContainer instead
 */
export default connect(
  (state, ownProps) => ({
    show: selectors.isPopupVisible(state, ownProps.popupId),
  }),
  (dispatch, ownProps) => ({
    onClose: () => {
      const { popupId, onClose } = ownProps
      if (onClose) {
        onClose()
      } else {
        dispatch(actions.close(popupId))
      }
    },
  })
)(Popup)
