import moment from 'moment-timezone'

import { FormikValidate, FormValue, Validator, TimeComparator } from '../types'
import { combineValidators } from './utils'

const DATE_PATTERN = '\\d{4}-\\d\\d-\\d\\d(T\\d\\d:\\d\\d:\\d\\d)?'

export const missingDate = (value: string): FormikValidate =>
  value === '' ? 'Missing Date' : undefined

export const dateIsAfter = (initialTimestamp: number) => (value: moment.Moment): FormikValidate => {
  if (moment.isMoment(value) && !value.isSameOrAfter(initialTimestamp)) {
    return `The date/time must be later than ${moment
      .tz(initialTimestamp, 'America/New_York')
      .format('MMM Do YYYY hh:mm A')}`
  }
}

export const stringDate = (value: string): FormikValidate => {
  if (!new RegExp(`^${DATE_PATTERN}$`).test(value) || !moment(value, 'YYYY-MM-DD').isValid()) {
    return 'Invalid date'
  }
}

/*
 * TIME VALIDATE
 */
export const timeCompareValidate = (
  time: string | undefined,
  comparator: TimeComparator,
  format?: string
) => (value: string): FormikValidate => {
  if (!time) {
    return undefined
  }

  const compareTime = moment(time, 'hh:mm')
  const valueTime = moment(value, 'hh:mm')

  return comparator(compareTime, format)(valueTime)
}

/*
 * MOMENT DATES
 */

export const future = (value: FormValue): FormikValidate => {
  if (moment(value).valueOf() < moment.utc().valueOf()) {
    return 'The date must be in the future'
  }
}

export const past = (value: FormValue): FormikValidate => {
  if (moment(value).valueOf() > moment.utc().valueOf()) {
    return 'The date must be in the past'
  }
}

export const beforeDate = (futureDate: moment.Moment, format?: string) => (
  value: moment.Moment
): FormikValidate =>
  !moment(value).isBefore(futureDate)
    ? `Date must precede the ${futureDate.format(format)}`
    : undefined

export const afterDate = (pastDate: moment.Moment, format?: string) => (
  value: moment.Moment
): FormikValidate =>
  !moment(value).isAfter(pastDate) ? `Date must exceed the ${pastDate.format(format)}` : undefined

export const sameOrBeforeDate = (futureDate: moment.Moment, format?: string) => (
  value: moment.Moment
): FormikValidate =>
  !moment(value).isSameOrBefore(futureDate)
    ? `Date must precede the ${futureDate.format(format)}`
    : undefined

export const sameOrAfterDate = (pastDate: moment.Moment, format?: string) => (
  value: moment.Moment
): FormikValidate =>
  !moment(value).isSameOrAfter(pastDate)
    ? `Date must exceed the ${pastDate.format(format)}`
    : undefined

/*
 * TIMESTAMPS
 */

export const timestamp = (value: number): FormikValidate =>
  !moment.unix(value).isValid() ? 'Invalid date' : undefined

export const futureTimestamp = combineValidators([timestamp, future])

export const afterDateTimestamp = (initialTimestamp: number): Validator =>
  combineValidators([timestamp, dateIsAfter(initialTimestamp)])
