import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'

import styles from './YesNoView.module.css'

const YesNoView = ({ value, className, topMargin }) => {
  const baseClassName = cx(
    styles.base,
    {
      [styles.checked]: value,
      [styles.topMargin]: topMargin,
    },
    className
  )

  return <div className={baseClassName} />
}

YesNoView.propTypes = {
  /** Value to display */
  value: PropTypes.bool.isRequired,
  /** Class name of generic toggle */
  className: PropTypes.string,
  /** If view displayed outside of form */
  topMargin: PropTypes.bool,
}

export default YesNoView
