import dotProp from 'dot-prop-immutable'
import { unset } from 'lodash-es'
import { Reducer } from 'redux'

import { isApiAction, isPagination, toPaginatedPayload } from './helpers'
import { ApiState, ApiActionState, PaginatedResponse } from '../../types'
import ApiError from '../../utils/ApiError'

const reducer: Reducer<ApiState> = (state = {}, action) => {
  if (!isApiAction(action)) return state

  const { type, payload, meta } = action

  let nextState: ApiActionState = { status: 'initial' }

  if (type.startsWith('API/PENDING/')) {
    nextState = { status: 'pending', requestAction: action }
  } else if (type.startsWith('API/SUCCESS/') && isPagination(action)) {
    const {
      meta: {
        api: { headers, paginatedField },
      },
    } = action

    const payload = action.payload as PaginatedResponse

    const { result, ...restPayload } = toPaginatedPayload(payload, paginatedField)
    const isReversed = headers['page-direction'] === 'next'
    const respHeaders = payload.__response__.headers
    const firstToken = headers['page-token'] ? respHeaders.get('page-first-token') : null
    const lastToken = !respHeaders.get('page-last') ? respHeaders.get('page-last-token') : null

    nextState = {
      status: 'success',
      pagination: {
        newestToken: isReversed ? lastToken : firstToken,
        oldestToken: isReversed ? firstToken : lastToken,
      },
      ...restPayload,
      result: isReversed ? result.reverse() : result,
      error: null,
    }
  } else if (type.startsWith('API/SUCCESS/')) {
    nextState = { status: 'success', result: meta.notSave ? null : payload, error: null }
    meta.notSave && unset(action.payload, '__response__')
  } else if (type.startsWith('API/FAILED/')) {
    nextState = { status: 'failed', error: action.payload as ApiError[] | ApiError | null }
  }

  return dotProp.merge(state, meta.api.id, nextState)
}

export default reducer
