import React from 'react'
import { Notification } from '..'

const ApiFailed = ({ className, errors }) =>
  <Notification className={className} kind='danger'>
    {errors.fields ? errors.fields.reduce((msg, err) => {
      return msg + `${err.field_name}: ${err.message}. `
    }, '') : errors.code}
  </Notification>

export default ApiFailed
