import React, { useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'
import { connect as formikConnect } from 'formik'
import cx from 'classnames'

import Button from '../../../elements/Button'
import ActionsDropdown from '../../../elements/ActionsDropdown'
import { useEnhancedForm, getShallowDiff } from '../../utils'
import styles from './FormModeToggle.module.css'

const FormModeToggle = ({
  formik,
  submitActionName,
  pendingActionName,
  actions,
  isEditMode,
  setEditMode,
  setReadMode,
  disableEdit,
  formToggleClassName,
}) => {
  const { values, initialValues, resetForm, isSubmitting } = formik
  const { resetSubmissionError } = useEnhancedForm()

  const { hasChanges } = useMemo(() => getShallowDiff(initialValues, values), [
    initialValues,
    values,
  ])

  const cancelEditingCallback = useCallback(() => {
    resetForm()
    resetSubmissionError()
    setReadMode()
  }, [resetForm, resetSubmissionError, setReadMode])

  return (
    <div className={cx(styles.actionPanel, formToggleClassName)}>
      {isEditMode ? (
        <>
          <Button
            className="margin-right-20"
            disabled={!hasChanges || isSubmitting}
            type="submit"
            key="submit"
          >
            {isSubmitting ? pendingActionName : submitActionName}
          </Button>
          <Button
            disabled={isSubmitting}
            kind="secondary"
            type="button"
            onClick={cancelEditingCallback}
            key="cancel"
          >
            Cancel
          </Button>
        </>
      ) : (
        <>
          {Array.isArray(actions) ? (
            <ActionsDropdown
              actions={[{ name: 'Edit', onClick: setEditMode }, ...actions]}
              staticSide="left"
            />
          ) : (
            <Button disabled={disableEdit} type="button" onClick={setEditMode}>
              Edit
            </Button>
          )}
        </>
      )}
    </div>
  )
}

FormModeToggle.propTypes = {
  submitActionName: PropTypes.string,
  pendingActionName: PropTypes.string,
  formik: PropTypes.object.isRequired,
  isEditMode: PropTypes.bool.isRequired,
  setEditMode: PropTypes.func.isRequired,
  setReadMode: PropTypes.func.isRequired,
  actions: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      onClick: PropTypes.func.isRequired,
    })
  ),
  disableEdit: PropTypes.bool,
  formToggleClassName: PropTypes.string,
}

FormModeToggle.defaultProps = {
  submitActionName: 'Save',
  pendingActionName: 'Saving...',
}

export default formikConnect(FormModeToggle)
