import React, { useCallback } from 'react'
import { Formik } from 'formik'
import PropTypes from 'prop-types'

import { getShallowDiff, useSubmissionErrorState, formatFormErrorMessage } from '../utils'
import { InnerFormWithContext } from './components'

const Form = ({
  onSubmit,
  initialValues,
  formatValuesBeforeSubmission,
  onFormInit,
  linkFormActions,
  hideSubmissionError,
  errorMap,
  isEditMode,
  setEditMode,
  setReadMode,
  staticContainer,
  className,
  children,
  ...restProps
}) => {
  const [submissionError, setSubmissionError, resetSubmissionError] = useSubmissionErrorState()

  const handleSubmitCallback = useCallback(
    (values, formikActions) => {
      const formattedValues =
        typeof formatValuesBeforeSubmission === 'function'
          ? formatValuesBeforeSubmission(values, initialValues)
          : values
      const { __files__ = {}, ...finalValues } = formattedValues

      const submitParams = {
        values: finalValues,
        initialValues,
        __files__,
        formikActions,
        enhancedActions: { setEditMode, setReadMode, setSubmissionError, resetSubmissionError },
      }
      const { diff } = getShallowDiff(initialValues, finalValues)

      return Promise.resolve(onSubmit({ ...submitParams, diff }))
        .then(() => {
          resetSubmissionError()

          if (typeof setReadMode === 'function') {
            setReadMode()
          }
        })
        .catch(error => {
          const errorMessage = formatFormErrorMessage(error, errorMap)

          setSubmissionError(errorMessage)
        })
        .finally(() => {
          formikActions.setSubmitting(false)
        })
    },
    [initialValues, onSubmit, formatValuesBeforeSubmission]
  )

  return (
    <Formik
      {...restProps}
      initialValues={initialValues}
      onSubmit={handleSubmitCallback}
      render={formik => (
        <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
          <InnerFormWithContext
            formik={formik}
            isEditMode={isEditMode}
            setEditMode={setEditMode}
            setReadMode={setReadMode}
            submissionError={submissionError}
            setSubmissionError={setSubmissionError}
            resetSubmissionError={resetSubmissionError}
            hideSubmissionError={hideSubmissionError}
            onFormInit={onFormInit}
            linkFormActions={linkFormActions}
            staticContainer={staticContainer}
            className={className}
          >
            {children}
          </InnerFormWithContext>
        </form>
      )}
    />
  )
}

Form.propTypes = {
  /** Initial values of the form (formik API) */
  initialValues: PropTypes.object.isRequired,
  /**
   * On submit form handler.
   *
   * The following object will be passed into:
   * {
   *   values,           - form values formatted by `formatValuesBeforeSubmission` (see prop below)
   *   initialValues,    - initial values for form (useful for comparing with `values`)
   *    __files__,        - files, that are spread out of values before
   *    formikActions,    - formik actions (useful for setting errors manually)
   *    enhancedActions   - enhanced actions (useful for setting submission error)
   *    diff              - diff between values and initialValues
   * }
   */
  onSubmit: PropTypes.func.isRequired,
  /**
   * Formats values when form submission is triggered but before calling `onSubmit` from props.
   * The result of this func will be passed as `values` to `onSubmit` handler.
   * Two arguments are passed to this func: `values`, `initialValues`.
   *
   * Common use-case - compute extra value depending on others, e.g. in car registration document .
   */
  formatValuesBeforeSubmission: PropTypes.func,
  /**
   * Hides default form submission error.
   * Default submission error is controlled by EnhancedForm context
   * and located at the top inside form (before children).
   *
   * Common use-case - you want to show submission error in custom place.
   */
  hideSubmissionError: PropTypes.bool,
  /**
   * Map that is used for humanazing submission errors.
   * Common errors will be humanized out-of-box.
   * Example: { ERR_SOME_ERROR: 'Some error normal text' }.
   */
  errorMap: PropTypes.object,
  /**
   * WARNING: This prop is only used when Form used within EditableCard. Don't pass it manually.
   */
  isEditMode: PropTypes.bool,
  /**
   * WARNING: This prop is only used when Form used within EditableCard. Don't pass it manually.
   */
  setEditMode: PropTypes.func,
  /**
   * WARNING: This prop is only used when Form used within EditableCard. Don't pass it manually.
   */
  setReadMode: PropTypes.func,
  /**
   * Function, that will be executed on form initialize phase.
   * Both formik and enhanced actions will be passed
   * to this func - ({ formik: { ...actions }, enhanced: {...actions} }).
   *
   * Common use-case - attach `onDMVScanComplete` to window, to handle autofill.
   */
  onFormInit: PropTypes.func,
  /**
   * In rare cases, where you want to lift both formik and enhanced actions up
   * ({ formik: { ...actions }, enhanced: {...actions} }), you can pass this func,
   * which should save link to formActions object to state. As an example, you could
   * take a look how this component saves actions via <FormInitializer> component,
   * to make them accessible in state for autofill implementation.
   *
   * Usually, you should use <WithForm>, <WithFormik> or <WithEnhancedForm> utility components,
   * which will pass necessary form actions as render-props inside form.
   */
  linkFormActions: PropTypes.func,
  /**
   * If form container should have position static.
   */
  staticContainer: PropTypes.bool,
  /**
   * Class name of form container.
   */
  className: PropTypes.string,
  /** Content of the form */
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.node]),
  /**
   * All rest props are considered as formik props and will be passed into <Formik> component.
   */
}

Form.defaultProps = {
  isEditMode: true,
  errorMap: {},
}

export default Form
