import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import ReactModal from 'react-modal'

/**
 * @deprecated Use Modal instead
 */
const Popup = ({ children, className, withoutCard, handleAfterOpen, isBigContent, onClose, show }) => {
  if (!show || !children) {
    return null
  }

  const childrenWithProps = React.Children.map(children, child => {
    if (!React.isValidElement(child)) {
      return child
    }
    return React.cloneElement(child, { onClose })
  })

  return (
    <ReactModal
      overlayClassName={cx('popup-overlay', { big: isBigContent })}
      className={cx(
        { card: !withoutCard, 'popup-inner-big': isBigContent, 'popup-inner-small': !isBigContent },
        className
      )}
      contentLabel="Popup"
      isOpen={show}
      onAfterOpen={handleAfterOpen}
      onRequestClose={onClose}
    >
      {childrenWithProps}
    </ReactModal>
  )
}

Popup.propTypes = {
  children: PropTypes.node,
  handleAfterOpen: PropTypes.func,
  onClose: PropTypes.func.isRequired,
  withoutCard: PropTypes.bool,
  show: PropTypes.bool
}

Popup.setAppElement = ReactModal.setAppElement

export default Popup
