import React, { useState, useMemo, useCallback, FC } from 'react'
import { FieldInputProps, useFormikContext } from 'formik'
import cx from 'classnames'

import { useOnClickOutside } from '../../../../hooks'
import { ErrorMessage } from '../../'
import {
  useEnhancedForm,
  useClearAutofill,
  useFormattedOptions,
  getErrorMessage,
} from '../../utils'
import {
  BasicGeneric,
  Generic,
  ChangeHandler,
  CustomChangeHandler,
  Options,
  FormValues,
} from '../../types'

import commonStyles from '../GenericCommon.module.css'
import styles from './GenericSelect.module.css'

export interface GenericSelectProps extends Generic {
  /** Data used for rendering select variants */
  options: Options
  /** Custom function that will be executed on change (new value and setValue will be passed in) */
  onChangeCustom?: CustomChangeHandler
  /** Custom placeholder, that will be used if no value is selected */
  placeholder?: string
}

const GenericSelect: FC<BasicGeneric &
  GenericSelectProps & {
    field: FieldInputProps<string>
  }> = ({
  field,
  disabled,
  onChangeCustom,
  placeholder = 'Please Choose',
  options,
  className,
  errorClassName,
}) => {
  const [isActive, setActive] = useState(false)

  const handleClickInputClick = useCallback(() => {
    disabled || setActive(!isActive)
  }, [setActive, disabled])

  const containerRef = useOnClickOutside(() => setActive(false))

  const form = useFormikContext<FormValues>()

  const { name, value } = field
  const errorMessage = getErrorMessage(name, form)

  const { isAutofilled, resetAutofill } = useEnhancedForm(name)
  const formattedOptions = useFormattedOptions(options)
  const optionsToShow = useMemo(() => [['', placeholder], ...formattedOptions], [
    placeholder,
    formattedOptions,
  ])

  useClearAutofill(isAutofilled, () => resetAutofill(name))

  const handleOptionClick = useCallback(
    val => {
      setActive(false)
      const setValue: ChangeHandler = val => form.setFieldValue(name, val)

      if (typeof onChangeCustom === 'function') {
        onChangeCustom(val, setValue)
      } else {
        setValue(val)
      }

      form.setFieldTouched(name, true)
    },
    [setActive, onChangeCustom, form]
  )

  const selectedOption = optionsToShow.find(([key]) => key === value) || ['', placeholder]

  const inputClasses = cx(commonStyles.input, styles.input, {
    [commonStyles.invalid]: errorMessage,
    [commonStyles.autofilled]: isAutofilled,
    [styles.disabled]: disabled,
    [styles.placeholder]: !value,
  })

  return (
    <div ref={containerRef} className={cx(styles.wrapper, className)}>
      <div className={inputClasses} onClick={handleClickInputClick}>
        {selectedOption && selectedOption[1]}
      </div>
      <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />

      {isActive && (
        <div className={styles.options}>
          {optionsToShow.map(([key, description], index) => (
            <button
              key={`${index}${key}`}
              className={cx(styles.option, {
                [styles.placeholder]: description === placeholder,
                [styles.selected]: key !== '' && key === selectedOption[0],
              })}
              onClick={() => handleOptionClick(key)}
            >
              {description}
            </button>
          ))}
        </div>
      )}
    </div>
  )
}

export default GenericSelect
