import React, { FC } from 'react'
import { FieldInputProps, useFormikContext } from 'formik'
import cx from 'classnames'
import uuid from 'uuid/v4'

import { ErrorMessage } from '../../'
import { useEnhancedForm, getErrorMessage } from '../../utils'
import { BasicGeneric, FormValues, Generic } from '../../types'

import styles from './GenericToggle.module.css'
import commonStyles from '../GenericCommon.module.css'

const GenericToggle: FC<BasicGeneric & Generic & { field: FieldInputProps<string> }> = ({
  field,
  errorClassName,
  className,
  disabled,
  ...restProps
}) => {
  const form = useFormikContext<FormValues>()

  const { name, value } = field
  const errorMessage = getErrorMessage(name, form)
  const { isEditMode } = useEnhancedForm()

  const id = uuid()

  const inputClasses = cx(styles.toggleCheckbox, { [commonStyles.invalid]: errorMessage })
  const labelClasses = cx(styles.toggleLabel, {
    [styles.checked]: value,
  })

  return (
    <div className={className}>
      <div className={styles.toggle}>
        <input
          {...field}
          {...restProps}
          id={id}
          className={inputClasses}
          checked={Boolean(value)}
          disabled={disabled}
          type="checkbox"
        />
        <label className={labelClasses} htmlFor={id} />
      </div>
      {isEditMode && <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />}
    </div>
  )
}

export default GenericToggle
