import React, { useCallback, FC } from 'react'
import moment from 'moment-timezone'
import Datetime from 'react-datetime'
import cx from 'classnames'

import { ErrorMessage } from '../../'
import { getErrorMessage } from '../../utils'
import { BasicGeneric, FormValues, Generic } from '../../types'

import 'react-datetime/css/react-datetime.css'
import './GenericDateTimePicker.css'
import commonStyles from '../GenericCommon.module.css'
import { FieldInputProps, useFormikContext } from 'formik'

export type FieldValue = string | Date | moment.Moment | undefined

export interface GenericDateTimePickerProps extends Generic {
  /** Format for date render */
  dateFormat?: string
  /** Format for time render or `false` to disable time */
  timeFormat?: string | boolean
  /** Input placeholder */
  placeholder?: string
}

const GenericDateTimePicker: FC<
  BasicGeneric &
    GenericDateTimePickerProps & {
      field: FieldInputProps<FieldValue>
    }
> = ({ field, errorClassName, dateFormat, timeFormat, placeholder, className, disabled }) => {
  const form = useFormikContext<FormValues>()

  const { value, name, onBlur } = field
  const errorMessage = getErrorMessage(name, form)

  const inputClassName = cx(commonStyles.input, {
    [commonStyles.invalid]: errorMessage,
  })

  const changeFieldValue = useCallback(
    value => {
      // 2020-08-30T03:59:59.999Z
      form.setFieldValue(name, value)
    },
    [form, name]
  )

  const handleBlur = useCallback(
    value => {
      form.setFieldValue(name, value)
      onBlur(name)
    },
    [onBlur, form, name]
  )

  const handleFocus = useCallback(() => {
    form.setFieldTouched(name, true)
  }, [form, name])

  return (
    <div className={className}>
      <Datetime
        onChange={changeFieldValue}
        dateFormat={dateFormat}
        timeFormat={timeFormat}
        value={value}
        onBlur={handleBlur}
        onFocus={handleFocus}
        inputProps={{ className: inputClassName, placeholder, disabled }}
      />
      <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />
    </div>
  )
}

export default GenericDateTimePicker
