import { setUserId } from '@juno/shared/analytics'
import { apiCall } from '@juno/shared/api'
import ApiError from '@juno/shared/api/ApiError'

import { clearCredentials } from '../utils/rest'
import { createConstants, resolved, pending } from '../utils/redux'
import { humanizeErrors } from '../utils/humanizeErrors'

export const constants = createConstants('auth', ['LOGIN', 'LOGOUT'])

export const actions = {
  login: ({ username, password }, endpoints, extraHeaders) => dispatch => {
    return apiCall('POST', endpoints.login, { username, password }, extraHeaders)
      .catch(err =>
        Promise.reject(
          humanizeErrors(err, {
            ERR_FORBIDDEN: 'Invalid username or password',
          })
        )
      )
      .then(profile =>
        apiCall('GET', endpoints.permissions)
          .then(permissions => {
            setUserId(profile.username)
            dispatch(actions.loginResolved(profile, permissions))
          })
          .catch(err =>
            Promise.reject(
              humanizeErrors(err, {
                ERR_FORBIDDEN: "You don't have permissions to view this page",
              })
            )
          )
      )
  },
  loginPending: () => ({
    type: pending(constants.LOGIN),
  }),
  loginResolved: (profile, permissions) => ({
    type: resolved(constants.LOGIN),
    payload: {
      ...profile,
      ...permissions,
    },
  }),
  logout: endpoints => dispatch => {
    clearCredentials()

    if (!endpoints) {
      return dispatch(actions.logoutResolve())
    }

    return apiCall('POST', endpoints.logout)
      .catch(() => dispatch(actions.logoutResolve()))
      .then(() => dispatch(actions.logoutResolve()))
  },
  logoutResolve: () => ({
    type: constants.LOGOUT,
  }),
  getUser: ({ permissions, profile }) => dispatch => {
    dispatch(actions.loginPending())

    return Promise.all([apiCall('GET', profile), apiCall('GET', permissions)])
      .then(([profile, permissions]) => dispatch(actions.loginResolved(profile, permissions)))
      .catch(err => {
        if ((err instanceof ApiError && err.isUnauthorized()) || err.code === 'ERR_FORBIDDEN') {
          dispatch(actions.logout())
        }
      })
  },
}

const initialState = {
  account: null,
  isLoading: false,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case pending(constants.LOGIN):
      return {
        isLoading: true,
      }

    case resolved(constants.LOGIN):
      return {
        account: action.payload,
        isLoading: false,
      }

    default:
      return state
  }
}
