import { get } from 'lodash-es'

import { Options, StringArrayOptions, EntriesArrayOptions, FormattedOptions } from '../types'

const isArrayOfStrings = (options: Options): options is StringArrayOptions => {
  if (!Array.isArray(options)) {
    return false
  }

  return options.every((option: unknown) => typeof option === 'string')
}

const isArrayOfObjects = (options: Options): options is EntriesArrayOptions => {
  if (!Array.isArray(options)) {
    return false
  }

  return options.every((option: unknown) => {
    if (option instanceof Object) {
      return 'key' in option && 'value' in option
    }
    return false
  })
}

export const formatOptions = (options: Options): FormattedOptions => {
  // Fallback for possible falsy values
  if (!options) {
    return []
  }

  // Format { key1:value1, key2:value2 } to the array of entries
  if (!Array.isArray(options)) {
    return Object.entries(options)
  }

  // Format [ 'value', 'value' ] to the array of entries
  if (isArrayOfStrings(options)) {
    return options.map(opt => [opt, opt])
  }

  // Format [ {key:'',value:''}, {key:'',value:''} ] to the array of entries
  if (isArrayOfObjects(options)) {
    return options.map(opt => [opt.key, opt.value])
  }

  // Already array of entries
  return options
}

export const findByKey = (key: string, options?: Options): string | undefined => {
  // Fallback for possible falsy values
  if (!options) {
    return key
  }

  // Find in { key1:value1, key2:value2 }
  if (!Array.isArray(options)) {
    return options[key]
  }

  // Find in [ 'value', 'value' ]
  if (isArrayOfStrings(options)) {
    return options.find(item => item === key)
  }

  // Find in [ {key:'',value:''}, {key:'',value:''} ]
  // Format [ {key:'',value:''}, {key:'',value:''} ] to the array of entries
  if (isArrayOfObjects(options)) {
    return get(options.find(opt => opt.key === key), 'value')
  }

  // Find in [ [key,value], [key,value] ]
  return get(options.find(opt => opt[0] === key), '[1]')
}
