import React, { useCallback, useMemo, FC } from 'react'
import { useFormikContext } from 'formik'
import DayPicker from 'react-day-picker'
import ReactInputMask from 'react-input-mask'
import cx from 'classnames'

import { useToggleState, useOnClickOutside } from '../../../../hooks'
import {
  PLACEHOLDER,
  MASK,
  MASK_CHAR,
  HANDLERS_BY_TYPE,
  fromCalendarToInputFormat,
  formatInitialMonth,
} from '../../utils/date-picker'
import { ErrorMessage } from '../../'
import { getErrorMessage } from '../../utils'
import { BasicGeneric, FormValues, Generic } from '../../types'

import '../../../elements/DayRangeSelect/DayRangeSelect.module.css'
import styles from './GenericDatePicker.module.css'
import commonStyles from '../GenericCommon.module.css'

export interface GenericDatePickerProps extends Generic {
  type: 'string' | 'timestamp'
  subtype?: 'endOfDay' | 'startOfDay'
}

const GenericDatePicker: FC<BasicGeneric & GenericDatePickerProps> = ({
  field,
  type,
  subtype,
  className,
  errorClassName,
  disabled,
}) => {
  const form = useFormikContext<FormValues>()

  const { value, name, onBlur } = field
  const [isCalendarShown, showCalendar, hideCalendar] = useToggleState(false)
  const { format, normalize } = useMemo(() => HANDLERS_BY_TYPE[type], [type])
  const containerRef = useOnClickOutside(hideCalendar)
  const errorMessage = getErrorMessage(name, form)
  const inputClasses = cx([commonStyles.input], className, {
    [commonStyles.invalid]: errorMessage,
  })

  const changeFieldValue = useCallback(
    value => {
      form.setFieldValue(name, value)
    },
    [form, name]
  )

  const handleInputChange = useCallback(
    event => {
      // 'react-input-mask' calls onChange also on focus/blur events, which is not needed for us to make calendar work properly
      if (event.type === 'change') {
        changeFieldValue(normalize(event.target.value, subtype))
        hideCalendar()
      }
    },
    [changeFieldValue, normalize, hideCalendar, subtype]
  )

  const handleDayClick = useCallback(
    date => {
      changeFieldValue(normalize(fromCalendarToInputFormat(date), subtype))
      hideCalendar()
    },
    [changeFieldValue, normalize, hideCalendar, subtype]
  )

  return (
    <div ref={containerRef} className={styles.container}>
      <ReactInputMask
        name={name}
        onBlur={onBlur}
        value={format(value)}
        onChange={handleInputChange}
        mask={MASK}
        maskChar={MASK_CHAR}
        placeholder={PLACEHOLDER}
        type="text"
        onFocus={showCalendar}
        className={inputClasses}
        disabled={disabled}
      />
      <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />
      <div className={styles.calendarContainer}>
        {isCalendarShown && (
          <DayPicker
            onDayClick={handleDayClick}
            initialMonth={formatInitialMonth(value)}
            className={styles.calendar}
            showOutsideDays
          />
        )}
      </div>
    </div>
  )
}

export default GenericDatePicker
