import React, { FC } from 'react'
import { useFormikContext } from 'formik'
import cx from 'classnames'

import { ErrorMessage } from '../../'
import { useEnhancedForm, useFormattedOptions, getErrorMessage } from '../../utils'
import { BasicGeneric, FormValues, Generic, Options } from '../../types'

import styles from './GenericRadios.module.css'

export interface GenericRadiosProps extends Generic {
  /** Data used for rendering corresponding radios */
  options?: Options
}

const GenericRadios: FC<BasicGeneric & GenericRadiosProps> = ({
  field,
  options,
  disabled,
  className,
  errorClassName,
}) => {
  const form = useFormikContext<FormValues>()

  const { name, value } = field
  const errorMessage = getErrorMessage(name, form)

  const { isEditMode } = useEnhancedForm()
  const formattedOptions = useFormattedOptions(options)

  const isDisabled = disabled || !isEditMode
  const radioGroupClasses = cx(styles.radioGroup, { [styles.invalid]: errorMessage })

  return (
    <div className={className}>
      <div className={radioGroupClasses}>
        {formattedOptions.map(([key, description]) => (
          <div key={key} className={styles.radioButton}>
            <label className={styles.label}>
              <input
                {...field}
                type="radio"
                className={styles.input}
                value={key}
                checked={key === value}
                disabled={isDisabled}
              />
              <span
                className={cx(styles.icon, {
                  [styles.checked]: key === value,
                  [styles.disabled]: isDisabled,
                })}
              />
              {description}
            </label>
          </div>
        ))}
      </div>
      <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />
    </div>
  )
}

export default GenericRadios
