export const capitalize = key => {
  if (typeof key === 'string' && key.length) {
    return key[0].toUpperCase() + key.slice(1).toLowerCase()
  } else {
    return key
  }
}

export const humanizeKey = key => {
  if (typeof key !== 'string') return key

  // todo: remove function and its usages in support and onboarding systems after BE-12221 is resolved
  if (key === 'suspended') return 'On-Hold'

  const text = key.replace(/_/g, ' ')
  return capitalize(text)
}

export const humanizeProviderOnboardingStatus = status => {
  if (status === 'bgcheck_pending') {
    return 'Pending BG Check'
  }

  return humanizeKey(status)
}

export const humanizeBackgroundCheckStatus = status => {
  if (status === 'pre_adverse') {
    return 'Pre Adverse Action'
  }

  return humanizeKey(status)
}


const SERVICE_TYPE_POSTFIX_L10N = {
  l1: 'Bliss',
  l2: 'Lux',
  l3: 'SUV'
}

export const humanizeServiceType = serviceType => {
  if (typeof serviceType !== 'string') {
    return ''
  }

  const postfix = serviceType.slice(-2)

  return SERVICE_TYPE_POSTFIX_L10N[postfix] || serviceType
}

export const humanizeMarket = marketId => {
  return (
    {
      nyc: 'New York',
      nj: 'New Jersey'
    }[marketId] || '-'
  )
}
