import React, { useState, useCallback } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'

import { Card, Row } from '../../'
import styles from './CollapsibleCard.module.css'

const CollapsibleCard = ({
  header,
  headerContent,
  onChange,
  collapsedByDefault,
  noHeaderMargin,
  expandOnClick,
  contentClassName,
  children,
  ...restProps
}) => {
  const [isExpanded, changeState] = useState(!collapsedByDefault)

  const reportEvent = useCallback(
    nextState => {
      if (typeof onChange === 'function') {
        const eventLabel = `${nextState ? 'Expand' : 'Collapse'}_${header.toLocaleUpperCase()}`
        onChange(eventLabel)
      }
    },
    [header, onChange]
  )

  const handleCardToggle = useCallback(() => {
    changeState(prevState => {
      const nextState = !prevState
      reportEvent(nextState)

      return nextState
    })
  }, [changeState, header, onChange])

  const handleHeaderContentClick = useCallback(() => {
    if (expandOnClick && !isExpanded) {
      const nextState = true
      reportEvent(nextState)
      changeState(nextState)
    }
  }, [isExpanded, changeState, expandOnClick, header, onChange])

  return (
    <Card
      customHeader={
        <>
          <Row className={cx('full', styles.clickableHeader)} onClick={handleCardToggle}>
            <div className={styles.iconContainer}>
              <span className={cx(styles.iconExpand, { [styles.rotated]: isExpanded })} />
            </div>
            <h3 className={cx(styles.header, { [styles.noHeaderMargin]: noHeaderMargin })}>
              {header}
            </h3>
          </Row>
          <div onClick={handleHeaderContentClick}>{headerContent}</div>
        </>
      }
      contentClassName={cx(contentClassName, [
        { [styles.noPadding]: !isExpanded, [styles.hidden]: !isExpanded },
      ])}
      {...restProps}
    >
      {children}
    </Card>
  )
}

CollapsibleCard.propTypes = {
  header: PropTypes.string.isRequired,
  headerContent: PropTypes.node,
  onChange: PropTypes.func,
  collapsedByDefault: PropTypes.bool,
  noHeaderMargin: PropTypes.bool,
  expandOnClick: PropTypes.bool,
  contentClassName: PropTypes.string,
  children: PropTypes.node,
}

export default CollapsibleCard
