import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import Sentry from '@juno/shared/error-tracking'

import CopyIcon from './copy.svg'
import styles from './QuickCopy.module.css'

class QuickCopy extends React.Component {
  static propTypes = {
    /** Value to show and copy (in case `customCopyValue` is absent) */
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    /** Google Analytics event label. If absent, reporting will be skipped */
    gaEventLabel: PropTypes.string,
    /** Custom value that will be copied instead of shown value */
    customCopyValue: PropTypes.string,
  }

  state = {
    clicked: false,
  }

  elementToCopy = React.createRef()

  handleClick = () => {
    const selection = window.getSelection()
    const range = document.createRange()
    const { gaEventLabel } = this.props

    range.selectNodeContents(this.elementToCopy.current)
    selection.removeAllRanges()
    selection.addRange(range)

    try {
      document.execCommand('copy')
      selection.removeAllRanges()
      this.setState({ clicked: true })

      if (gaEventLabel) {
        window.ga('send', 'event', {
          eventCategory: 'UI',
          eventAction: 'Click',
          eventLabel: gaEventLabel,
        })
      }

      setTimeout(() => {
        this.setState({ clicked: false })
      }, 500)
    } catch (exception) {
      Sentry.captureException(exception)
    }
  }

  render() {
    const { customCopyValue, value } = this.props

    if (!value) {
      return null
    }

    return (
      <>
        <span className={styles.quickCopy}>
          <span>{value}&nbsp;</span>
          <div
            onClick={this.handleClick}
            className={cx(styles.iconContainer, { [styles.clicked]: this.state.clicked })}
          >
            <CopyIcon />
          </div>
        </span>
        <div className={styles.hidden} ref={this.elementToCopy}>
          {customCopyValue || value}
        </div>
      </>
    )
  }
}

export default QuickCopy
