import React, { useCallback, FC } from 'react'
import { useFormikContext } from 'formik'
import cx from 'classnames'

import { ErrorMessage } from '../../'
import { useEnhancedForm, useClearAutofill, getErrorMessage } from '../../utils'
import { BasicGeneric, InputGeneric, ChangeHandler, FormValues } from '../../types'

import commonStyles from '../GenericCommon.module.css'

const GenericInput: FC<BasicGeneric & InputGeneric & React.HTMLProps<HTMLInputElement>> = ({
  field,
  format,
  normalize,
  onChangeCustom,
  className,
  errorClassName,
  ...restProps
}) => {
  const form = useFormikContext<FormValues>()

  const { value, name } = field
  const errorMessage = getErrorMessage(name, form)

  const { isAutofilled, resetAutofill } = useEnhancedForm(name)
  useClearAutofill(isAutofilled, () => resetAutofill(name))

  const inputClasses = cx([commonStyles.input], {
    [commonStyles.invalid]: errorMessage,
    [commonStyles.autofilled]: isAutofilled,
  })
  const formattedValue = typeof format === 'function' ? format(value) : value

  const handleChangeCallback = useCallback(
    event => {
      let newValue = event.target.value
      const setValue: ChangeHandler = val => form.setFieldValue(name, val)

      if (typeof onChangeCustom === 'function') {
        onChangeCustom(newValue, setValue)
        return
      }

      if (typeof normalize === 'function') {
        newValue = normalize(newValue)
      }

      setValue(newValue)
    },
    [form, onChangeCustom, normalize]
  )

  return (
    <div className={className}>
      <input
        autoComplete="off"
        id={name}
        {...field}
        {...restProps}
        onChange={handleChangeCallback}
        value={formattedValue || ''}
        className={inputClasses}
      />
      <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />
    </div>
  )
}

export default GenericInput
