import PropTypes from 'prop-types'
import React from 'react'
import cx from 'classnames'

export default class Alert extends React.Component {
  static propTypes = {
    className: PropTypes.string.isRequired, // success, info, warning, danger
    onClose: PropTypes.func,
    children: PropTypes.node,
    hasCloseBtn: PropTypes.bool,
  }

  constructor(props) {
    super(props)
    this.state = { isClosed: false }
  }

  UNSAFE_componentWillReceiveProps() {
    this.setState({ isClosed: false })
  }

  handleClick = () => {
    this.setState({ isClosed: true })
    if (this.props.onClose) this.props.onClose()
  }

  render() {
    return this.state.isClosed ? null : (
      <div className={cx('notification', this.props.className)}>
        {this.props.hasCloseBtn && (
          <span className="notification__close" onClick={this.handleClick}>
            <span>&times;</span>
          </span>
        )}
        {this.props.children}
      </div>
    )
  }
}
