import React, { Component, ReactNode } from 'react'
import cx from 'classnames'
import Sentry from '@juno/shared/error-tracking'

import Notification from '../elements/Notification'

import styles from './Card.module.css'

interface CardProps {
  header?: ReactNode
  customHeader?: ReactNode
  noBottomPadding?: boolean
  className?: string
  contentClassName?: string
}

interface CardState {
  error: boolean
}

class Card extends Component<CardProps, CardState> {
  state = {
    error: false,
  }

  componentDidCatch(error: Error | null, errorInfo: object): void {
    this.setState({ error: true })
    console.warn(`Caught the following error in "${this.props.header}" GROUP:\n`, error) // eslint-disable-line no-console
    console.warn('Additional error info:', errorInfo) // eslint-disable-line no-console
    Sentry.captureException(error, { extra: errorInfo })
  }

  render(): ReactNode {
    const {
      className,
      header,
      customHeader,
      noBottomPadding,
      children,
      contentClassName,
      ...restProps
    } = this.props

    const hasHeader = header || customHeader

    const content = this.state.error ? (
      <Notification kind="danger">Something went wrong</Notification>
    ) : (
      children
    )

    const cardClassName = cx(styles.card, className)
    const insideClassName = cx(styles.contentContainer, contentClassName, {
      [styles.withHeader]: hasHeader,
      [styles.noBottomPadding]: noBottomPadding,
    })

    return (
      <div className={cardClassName} {...restProps}>
        {hasHeader && (
          <div className={styles.headerContainer}>
            {header && <h3 className={styles.header}>{header}</h3>}
            {customHeader}
          </div>
        )}

        {content && <div className={insideClassName}>{content}</div>}
      </div>
    )
  }
}

export default Card
