import { combineReducers } from 'redux'
import update from 'immutability-helper'
import { constants } from '../redux/auth'

// todo: add usage examples to redux utils

export function createConstants (prefix, types) {
  return types.reduce((constants, type) => {
    constants[type] = `${prefix}/${type}`
    return constants
  }, {})
}

export const createAction = type => payload => ({
  type,
  payload
})

export const pending = type => `${type}_PENDING`
export const resolved = type => `${type}_SUCCESS`
export const rejected = type => `${type}_FAILED`

export const apiReducer = (state, normalizedData) => update(state, {
  result: { $merge: normalizedData.result },
  entities: { $merge: normalizedData.entities }
})

export const createReducer = (initialState, handlers) => (
  state = initialState,
  action
) => {
  if (handlers.hasOwnProperty(action.type)) {
    return handlers[action.type](state, action)
  } else {
    return state
  }
}

export const enhanceReducers = reducers => (state, action) => {
  state = action.type === constants.LOGOUT ? {} : state

  return combineReducers(reducers)(state, action)
}
