import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import ReactModal from 'react-modal'

import { Card } from '../../'
import styles from './Modal.module.css'

const Modal = ({ onClose, show, cardProps, withoutCard, handleAfterOpen, isBigContent, className, children }) => {
  if (!show || !children) {
    return null
  }

  const childrenWithProps = React.Children.map(children, child => {
    if (!React.isValidElement(child)) {
      return child
    }
    return React.cloneElement(child, { onClose })
  })

  const childrenWithContainer = React.createElement(
    withoutCard ? React.Fragment : Card,
    withoutCard ? {} : cardProps,
    childrenWithProps
  )

  return (
    <ReactModal
      isOpen={show}
      onAfterOpen={handleAfterOpen}
      onRequestClose={onClose}
      overlayClassName={cx(styles.overlay, { [styles.overlayBig]: isBigContent })}
      className={cx([isBigContent ? styles.innerBig : styles.innerSmall], className)}
      contentLabel="Modal"
    >
      {childrenWithContainer}
    </ReactModal>
  )
}

Modal.propTypes = {
  show: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  cardProps: PropTypes.object,
  handleAfterOpen: PropTypes.func,
  withoutCard: PropTypes.bool,
  isBigContent: PropTypes.bool,
  className: PropTypes.string,
  children: PropTypes.node
}

Modal.defaultProps = {
  cardProps: {}
}

Modal.setAppElement = ReactModal.setAppElement

export default Modal
