import React, { FC } from 'react'
import { useField } from 'formik'

import { GenericPhoneInput, FieldBlock } from '../../'
import { renderReadonlyValue, renderEditableField } from '../../utils'
import { Field, InputGeneric } from '../../types'

const PhoneInput: FC<Field & InputGeneric> = ({
  name,
  label,
  fieldClassName,
  formatReadonlyValue,
  inputClassName,
  validate,
  ...restProps
}) => {
  const [field] = useField({ name, validate })

  return (
    <FieldBlock label={label} fieldClassName={fieldClassName} name={field.name}>
      {renderEditableField(
        <GenericPhoneInput field={field} className={inputClassName} {...restProps} />,
        renderReadonlyValue(field.value, formatReadonlyValue)
      )}
    </FieldBlock>
  )
}

export default PhoneInput
