import uuid from 'uuid/v4'

import messages from './messages'
import ApiError from './ApiError'

/**
 * @param {string} method
 * @param {string} url
 * @param {Object} [reqBody]
 * @param {Object.<string, string>} [extraHeaders]
 * @return {Promise}
 */
export const httpCall = (method, url, reqBody, extraHeaders) => {
  const options = {
    method: method,
    credentials: 'same-origin',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'X-Request-Id': uuid(),
      ...extraHeaders,
    },
  }

  if (reqBody != null) {
    const isFormData = reqBody instanceof window.FormData
    options.body = isFormData ? reqBody : JSON.stringify(reqBody)
    if (isFormData) delete options.headers['Content-Type']
  }

  return window.fetch(url, options)
}

/**
 * @param {string} method
 * @param {string} url
 * @param {Object} [reqBody]
 * @param {Object.<string, string>} [extraHeaders]
 * @param {Object} [options]
 * @return {Promise}
 */
export const apiCall = (method, url, reqBody, extraHeaders, options = {}) =>
  new Promise((resolve, reject) => {
    const { prefix = process.env.JUNO_API_BASE_URL } = options
    const compiledUrl = `${prefix}${url}`

    httpCall(method.toUpperCase(), compiledUrl, reqBody, {
      ...extraHeaders,
    })
      .then(res => {
        if (res.status === 401) {
          reject(new ApiError(messages.alert.unauthorized, ApiError.UNAUTHORIZED))
        }

        const contentType = res.headers.get('content-type')
        if (contentType.includes('application/json')) {
          res.json().then(json => {
            if (res.ok) {
              resolve(json)
            } else {
              reject(json)
            }
          })
        } else {
          reject(new ApiError(messages.alert.panic, ApiError.PANIC))
        }
      })
      .catch(() => {
        reject(new ApiError(messages.alert.panic, ApiError.PANIC))
      })
  })

/**
 * GET
 * @param url
 * @param prefix
 * @return {Promise}
 */
export function get(url, prefix = process.env.JUNO_API_BASE_URL) {
  return httpCall('GET', `${prefix}${url}`, null, { 'X-Requested-With': 'XMLHttpRequest' })
}

/**
 * POST
 * @param url
 * @param data
 * @param prefix
 * @return {Promise}
 */
export function post(url, data, prefix = process.env.JUNO_API_BASE_URL) {
  return httpCall('POST', `${prefix}${url}`, data, { 'X-Requested-With': 'XMLHttpRequest' })
}

/**
 * Get recaptcha token and return promise
 * @param {string} action
 * @param {string} recaptchaSiteKey
 * @return {Promise}
 */
export function getRecaptchaToken(action, recaptchaSiteKey) {
  if (!recaptchaSiteKey) {
    throw new Error('No reCAPTCHA site key set')
  }

  const recaptcha = window.grecaptcha

  if (recaptcha) {
    return new Promise((resolve, reject) => {
      recaptcha.ready(() => {
        recaptcha.execute(recaptchaSiteKey, { action }).then(
          token => {
            const headers = {
              'X-Recaptcha-Token': token,
            }

            return resolve({ token, headers })
          },
          error => reject(error)
        )
      })
    })
  }

  throw new Error('reCAPTCHA is not loaded')
}
