import { connect } from 'react-redux'
import Modal from '../../components/elements/Modal/Modal'
import { actions, selectors } from './redux'

export default connect(
  (state, ownProps) => ({
    show: selectors.isPopupVisible(state, ownProps.modalId),
  }),
  (dispatch, ownProps) => ({
    onClose: () => {
      const { modalId, onClose } = ownProps
      if (onClose) {
        onClose()
      } else {
        dispatch(actions.close(modalId))
      }
    },
  })
)(Modal)
