import React, { FC } from 'react'
import { useField } from 'formik'

import { FieldBlock, GenericSelect } from '../../'
import { findByKey, renderEditableField } from '../../utils'
import { GenericSelectProps } from '../../input-generics/GenericSelect/GenericSelect'
import { CustomFormatReadonly, Field, FormValue, Options } from '../../types'

const renderReadonlyValue = (
  value: FormValue,
  options: Options,
  formatter?: CustomFormatReadonly
) => {
  if (typeof formatter === 'function') {
    return formatter(value)
  }

  return (value && findByKey(value, options)) || ''
}

const Select: FC<Field & GenericSelectProps> = ({
  name,
  label,
  fieldClassName,
  formatReadonlyValue,
  inputClassName,
  options,
  validate,
  ...restProps
}) => {
  // TODO: Remove after https://github.com/jaredpalmer/formik/issues/1961 resolved
  // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
  // @ts-ignore
  const [field] = useField<string>({ name, validate })

  return (
    <FieldBlock label={label} fieldClassName={fieldClassName} name={field.name}>
      {renderEditableField(
        <GenericSelect field={field} options={options} className={inputClassName} {...restProps} />,
        renderReadonlyValue(field.value, options, formatReadonlyValue)
      )}
    </FieldBlock>
  )
}

export default Select
