import React, { FC } from 'react'
import cx from 'classnames'

import { Offset } from '../types'

import styles from './Column.module.css'

interface ColumnProps {
  className?: string
  offset?: Offset
  size?: 'auto' | '15' | '20' | '25' | '30' | '40' | '50' | '60' | '70' | '75' | '80' | '100'
}

const Column: FC<ColumnProps> = ({ offset, size, className, children, ...restProps }) => {
  const baseClassName = cx(
    styles.base,
    styles[`offset-${offset}`],
    styles[`size-${size}`],
    className
  )

  return (
    <div className={baseClassName} {...restProps}>
      {children}
    </div>
  )
}

export default Column
