// todo: there's an issue with saturated onClose prop to all first level children of prop
//       dat's making impossible to use Notification instead of Alert
import PropTypes from 'prop-types'
import React, { useCallback, useState } from 'react'
import { connect } from 'react-redux'

import defaultMessages from '../constants/messages'
import { Alert, Button, ButtonGroup } from './'
import { Form } from './formik'
import { Popup as Modal } from '../containers'
import { actions } from '../containers/popups/redux' // todo: tweak exports

const ActionModal = ({
  action,
  children,
  className,
  dispatch,
  errors,
  headerText,
  initialValues,
  modalId,
  onSuccessCallback,
  performText,
  performKind,
  prompText,
  successMessage,
  formatValuesBeforeSubmission,
  modalRestProps,
  ...restFormProps
}) => {
  const [submissionIsSuccessful, setSuccessfulSubmission] = useState(false)

  const submitForm = useCallback(
    params => {
      const promise = dispatch(action(params.values))

      promise.then((...resp) => {
        setSuccessfulSubmission(true)

        if (onSuccessCallback) onSuccessCallback(params, ...resp)
      })

      return promise
    },
    [action, onSuccessCallback, dispatch, setSuccessfulSubmission]
  )

  const closeModal = useCallback(() => {
    // todo: tweak Modal (ex-Popup) component to expose onClose
    dispatch(actions.close(modalId))
    setSuccessfulSubmission(false)
  }, [setSuccessfulSubmission, modalId, dispatch])

  return (
    <Modal popupId={modalId} className={className} {...modalRestProps}>
      <h3 className="title-common">{headerText}</h3>
      {submissionIsSuccessful && successMessage && (
        <Alert className="success">{successMessage}</Alert>
      )}
      {!submissionIsSuccessful && prompText}

      <Form
        onSubmit={submitForm}
        initialValues={initialValues}
        errorMap={errors}
        isEditMode={!submissionIsSuccessful}
        formatValuesBeforeSubmission={formatValuesBeforeSubmission}
        {...restFormProps}
      >
        {(formik, context) => (
          <>
            {typeof children === 'function' ? children(formik, context) : children}

            <ButtonGroup>
              <Button kind="secondary" type="button" onClick={closeModal}>
                {submissionIsSuccessful ? 'Close' : 'Cancel'}
              </Button>
              {!submissionIsSuccessful && (
                <Button type="submit" kind={performKind}>
                  {performText}
                </Button>
              )}
            </ButtonGroup>
          </>
        )}
      </Form>
    </Modal>
  )
}

ActionModal.defaultProps = {
  initialValues: {},
  performText: 'Perform',
  modalRestProps: {},
  successMessage: defaultMessages.alert.success,
}

ActionModal.propTypes = {
  action: PropTypes.func.isRequired,
  children: PropTypes.node,
  className: PropTypes.string,
  errors: PropTypes.object,
  headerText: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
  initialValues: PropTypes.object.isRequired,
  modalId: PropTypes.string.isRequired,
  onSuccessCallback: PropTypes.func,
  performText: PropTypes.string,
  performKind: PropTypes.string,
  successMessage: PropTypes.string,
  prompText: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  formatValuesBeforeSubmission: PropTypes.func,
  modalRestProps: PropTypes.object,
}

export default connect()(ActionModal)
