import React from 'react'
import PropTypes from 'prop-types'
import DayPicker, { DateUtils } from 'react-day-picker'
import moment from 'moment-timezone'

import './DayRangeSelect.module.css'
import { switchTimezoneWithoutChangingValues as switchTZ } from '../../../utils/datetime'

const LOCAL_USER_TIMEZONE = moment.tz.guess()

export default class DayRangeSelect extends React.Component {
  static defaultProps = {
    timezone: 'America/New_York',
    value: {
      from: null,
      to: null,
    },
  }

  static propTypes = {
    onChange: PropTypes.func.isRequired,
    timezone: PropTypes.string,
    value: PropTypes.shape({
      from: PropTypes.object,
      to: PropTypes.object,
    }),
    disablePastDays: PropTypes.bool,
  }

  static requireIntervalSelected = val => val && val.from && val.to

  switchToLocal = dateInUtc => {
    if (!dateInUtc) return

    const mdate = moment.utc(dateInUtc).tz(this.props.timezone)
    return new Date(switchTZ(mdate, LOCAL_USER_TIMEZONE).valueOf())
  }

  handleDayClick = (dayInLocal, { disabled }) => {
    if (disabled) {
      return
    }

    const { from: fromInUtc, to: toInUtc } = this.props.value
    let rangeInLocal = DateUtils.addDayToRange(dayInLocal, {
      from: this.switchToLocal(fromInUtc),
      to: this.switchToLocal(toInUtc),
    })

    if (rangeInLocal.from !== null || rangeInLocal.to !== null) {
      rangeInLocal = {
        from: moment(rangeInLocal.from).startOf('day'),
        to: moment(rangeInLocal.to || rangeInLocal.from).endOf('day'),
      }
      this.props.onChange({
        from: switchTZ(rangeInLocal.from, this.props.timezone).utc(),
        to: switchTZ(rangeInLocal.to, this.props.timezone).utc(),
      })
    } else {
      this.props.onChange({
        from: null,
        to: null,
      })
    }
  }

  getDisabledPastDays = () => {
    const currentTimeInLocale = this.switchToLocal(moment.utc())
    const startOfDayInLocale = moment(currentTimeInLocale).startOf('day')

    return { before: new Date(startOfDayInLocale) }
  }

  disabledDays = this.props.disablePastDays && this.getDisabledPastDays()

  render() {
    const { from: fromInUtc, to: toInUtc } = this.props.value
    const from = this.switchToLocal(fromInUtc)
    const to = this.switchToLocal(toInUtc)

    const startingMonth = from && new Date(from.getFullYear(), from.getMonth())

    return (
      <DayPicker
        numberOfMonths={1}
        disabledDays={this.disabledDays}
        initialMonth={startingMonth}
        selectedDays={[from, { from, to }]}
        onDayClick={this.handleDayClick}
      />
    )
  }
}
