import { createConstants, createReducer } from '../../utils/redux'
import dotProp from 'dot-prop-immutable'

const constants = createConstants('popups', ['OPEN', 'CLOSE'])

export const actions = {
  open: (popupId) => ({
    type: constants.OPEN,
    payload: popupId
  }),
  close: (popupId) => ({
    type: constants.CLOSE,
    payload: popupId
  })
}

const initialState = {}

const isPopupVisible = (state, popupId) => Boolean(state.popups[popupId])

export const selectors = {
  isPopupVisible
}

export default createReducer(initialState, {
  [constants.OPEN]: (state, action) => {
    return dotProp.set(state, [action.payload], true)
  },
  [constants.CLOSE]: (state, action) => {
    return dotProp.delete(state, [action.payload])
  }
})
