import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'

export default class Userpic extends React.Component {
  static propTypes = {
    children: PropTypes.element,
    src: PropTypes.string.isRequired,
    originSrc: PropTypes.string,
    className: PropTypes.string,
  }

  state = {
    imageUrl: null,
  }

  componentDidMount() {
    this.loadUserpic(this.props)
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.src !== this.props.src || nextProps.originSrc !== this.props.originSrc) {
      this.loadUserpic(nextProps)
    }
  }

  componentWillUnmount() {
    this.unmounted = true
  }

  loadUserpic({ src, originSrc }) {
    if (!src && !originSrc) return

    const avatar = new window.Image()
    avatar.src = src
    avatar.onload = () => this.setImageUrl(src)
    avatar.onerror = () => {
      const origin = new window.Image()
      origin.src = originSrc
      origin.onload = () => this.setImageUrl(originSrc)
    }
  }

  setImageUrl = url => {
    if (this.unmounted) {
      return
    }
    return this.setState({ imageUrl: url })
  }

  render() {
    const { imageUrl } = this.state
    return (
      <div
        className={cx('userpic', this.props.className)}
        style={{ backgroundImage: imageUrl && `url(${imageUrl})` }}
      >
        {this.props.children}
      </div>
    )
  }
}
