import messages from '../constants/messages'

export default class ApiError {
  /**
   * @param {Object | string} err
   * @param {string} [type]
   */
  constructor (err, type) {
    // TODO: currently isn't compatible with server error format
    this.errors = typeof err === 'string'
      ? {__all__: [err]}
      : err.errors
    this._type = type
  }

  static UNAUTHORIZED = 'unauthorized'
  static FORBIDDEN = 'forbidden'
  static PANIC = 'panic'

  /**
   * @param {string} responseStatus
   * @param {Object} [responseBody]
   */
  static wrapError (responseStatus, responseBody) {
    switch (responseStatus) {
      case 400:
        return new ApiError(messages.alert.error)
      case 401:
        return new ApiError(responseBody, ApiError.UNAUTHORIZED)
      case 403:
        return new ApiError(responseBody, ApiError.FORBIDDEN)
      case 500:
      default:
        return new ApiError(messages.alert.panic, ApiError.PANIC)
    }
  }

  isUnauthorized () {
    return this._type === ApiError.UNAUTHORIZED
  }

  isForbidden () {
    return this._type === ApiError.FORBIDDEN
  }

  isPanic () {
    return this._type === ApiError.PANIC
  }
}
