import { ApiState, ApiActionState, ApiAction } from '../../types'
import { get } from 'lodash-es'

const initialState: ApiActionState = { status: 'initial' }

export const getActionState = <T = undefined>(
  state: ApiState,
  apiAction: ApiAction<T>
): ApiActionState => state[get(apiAction, 'meta.api.id')] || initialState

export const selectIfActionIsCompleted = <T = undefined>(
  state: ApiState,
  action: ApiAction<T>
): boolean => {
  const actionState = getActionState(state, action)

  return actionState.status === 'success'
}

export const selectIfAllActionsAreCompleted = <T = undefined>(
  state: ApiState,
  actions: ApiAction<T>[]
): boolean => {
  const statesOfActions = actions.map(action => getActionState(state, action))

  return statesOfActions.every(state => ['success', 'failed'].includes(state.status))
}

export const selectIfRequestFailsWithErrorCode = <T = undefined>(
  state: ApiState,
  action: ApiAction<T>,
  errorCode: number
): boolean => {
  const actionState = getActionState(state, action)

  return get(actionState, 'error.code') === errorCode
}

export const combineMultipleSelectors = <T>(
  state: T,
  selectors: Array<(state: T) => unknown>
): Array<unknown> => selectors.map(selector => selector(state))

const selectors = {
  getActionState,
  selectIfActionIsCompleted,
  selectIfAllActionsAreCompleted,
  selectIfRequestFailsWithErrorCode,
  combineMultipleSelectors,
}

export default selectors
