import * as helpers from './helpers'

export const initializeGAScript = () => {
  ;(function(i, s, o, g, r, a, m) {
    i['GoogleAnalyticsObject'] = r
    ;(i[r] =
      i[r] ||
      function() {
        ;(i[r].q = i[r].q || []).push(arguments)
      }),
      (i[r].l = 1 * new Date())
    ;(a = s.createElement(o)), (m = s.getElementsByTagName(o)[0])
    a.async = 1
    a.src = g
    m.parentNode.insertBefore(a, m)
  })(window, document, 'script', 'https://www.google-analytics.com/analytics.js', 'ga')
}

const showSetupInstructions = () => {
  /* eslint-disable-next-line no-console */
  console.error(
    'Failed to send data to Google Analytics because it is not initialized.' +
      'Use "init(key)" script from "@juno/ui-private/redux/analytics" before sending any data.'
  )
}

export const init = key => {
  initializeGAScript()
  if (process.env.JUNO_ENV !== 'production') {
    // To debug analytics locally, please use 'Google Analytics Debugger' plugin for Chrome
    window.ga('set', 'sendHitTask', null)
  } else {
    window.ga('create', key, { storage: 'none' })
  }
}

export function initializeGoogleAnalytics() {
  if (process.env.JUNO_ENV === 'production') {
    window.ga('create', process.env.JUNO_GA_KEY, 'auto')
  }
}

export const send = (type, ...rest) => {
  if (window.ga) {
    window.ga('send', type, ...rest)
  } else {
    showSetupInstructions()
  }
}

export const sendEvent = (...rest) => {
  send('event', ...rest)
}

export const setUserId = email => {
  if (window.ga) {
    window.ga('set', 'userId', email)
  } else {
    showSetupInstructions()
  }
}

export const sendPageVisit = (url, additionalFields = {}) =>
  send('pageview', {
    location: helpers.replaceGuid(url || document.location.pathname),
    ...additionalFields,
  })

export const sendTiming = ({ url, method, stamp, category, failureReason }) =>
  send('timing', {
    timingVar: helpers.replaceGuid(url || document.location.pathname),
    timingValue: stamp,
    timingCategory: category || `ajax ${method}`,
    timingLabel: failureReason ? `${method}: ${failureReason}` : method,
  })

export const sendEventAction = (url, method) =>
  sendEvent({
    eventCategory: 'Action',
    eventAction: helpers.formatEventAction(url, method),
  })

export const sendClickEvent = (category, label) =>
  sendEvent({
    eventAction: 'Click',
    eventCategory: category,
    eventLabel: label,
  })

export const sendGlobalNavClickEvent = sendClickEvent.bind(null, 'Global_Navigation')
export const sendSearchClickEvent = sendClickEvent.bind(null, 'Search')
