import React from 'react'
import PropTypes from 'prop-types'
import onClickOutside from 'react-onclickoutside'
import cx from 'classnames'

import Button from '../Button'
import DayRangeSelect from '../DayRangeSelect'
import ListSelect from '../ListSelect'

import SortIcon from '../../assets/icons/sort-filter-icon.svg'

class FilterTH extends React.Component {
  static propTypes = {
    className: PropTypes.string,
    items: PropTypes.array,
    onChange: PropTypes.func.isRequired,
    type: PropTypes.oneOf(['date-range', 'list']).isRequired,
  }

  state = {
    currentFilter: null,
    filterShown: false,
    preservedFilter: null,
  }

  toggleFilter = () => {
    this.setState(({ filterShown }) => {
      document[filterShown ? 'removeEventListener' : 'addEventListener'](
        'keydown',
        this.handleKeyDown
      )

      return {
        filterShown: !filterShown,
      }
    })
  }

  handleChange = currentFilter => {
    this.setState({
      currentFilter,
    })
  }

  resetValues = () => {
    this.setState(({ preservedFilter }) => ({ currentFilter: preservedFilter }), this.toggleFilter)
  }

  handleClickOutside = () => {
    if (this.state.filterShown) {
      this.resetValues()
    }
  }

  handleKeyDown = event => {
    if (event.key === 'Escape') {
      this.resetValues()
    }
  }

  clearSelection = () => {
    this.setState({ currentFilter: null }, this.applyFilter)
  }

  applyFilter = () => {
    this.setState(
      ({ currentFilter }) => ({
        preservedFilter: currentFilter,
      }),
      () => {
        this.props.onChange(this.state.currentFilter)
        this.toggleFilter()
      }
    )
  }

  render() {
    const { children, items, className, type } = this.props
    const { filterShown, currentFilter } = this.state
    const actions = {
      value: currentFilter || undefined,
      onChange: this.handleChange,
    }

    return (
      <th className={cx('filter-header', type, className)} onClick={this.toggleFilter}>
        {children}{' '}
        <SortIcon
          className={cx('filter-header__icon', { active: currentFilter && !filterShown })}
        />
        {filterShown && (
          <div
            className="filter-popup"
            onClick={event => {
              event.stopPropagation()
            }}
          >
            {type === 'list' && <ListSelect items={items} {...actions} />}
            {type === 'date-range' && <DayRangeSelect {...actions} />}

            <div className="filter-popup__actions">
              <Button kind="secondary" onClick={this.clearSelection} className="full">
                Clear
              </Button>
              <Button onClick={this.applyFilter} className="full">
                Apply
              </Button>
            </div>
          </div>
        )}
      </th>
    )
  }
}

export default onClickOutside(FilterTH)
