// todo: typescriptify

import React, { Children, ReactNode } from 'react'

import styles from './SideMenu.module.css'

interface SideMenuProps {
  children: ReactNode[]
  gaCategory: string
}

const SideMenu = ({ children, gaCategory }: SideMenuProps) => (
  <div className={styles.sidemenu}>
    <ul className={styles.list}>
      {Children.map(children, innerChildren => {
        const secondLevel = innerChildren.props.secondLevel

        // todo: change to make working with Fragment
        if (secondLevel) {
          const saturatedSecondLevelChildren = Children.map(secondLevel, secondLevelChild =>
            React.cloneElement(secondLevelChild, {
              gaCategory,
              parent: innerChildren.props.label,
            })
          )

          return React.cloneElement(innerChildren, {
            gaCategory,
            secondLevel: saturatedSecondLevelChildren,
          })
        }

        return React.cloneElement(innerChildren, { gaCategory })
      })}
    </ul>
  </div>
)

export default React.memo(SideMenu)
