import moment from 'moment-timezone'
import { DateUtils } from 'react-day-picker'

import { switchTimezoneWithoutChangingValues } from '../../../utils/datetime'

export const DATEPICKER_SUBTYPES = { END_OF_DAY: 'endOfDay', START_OF_DAY: 'startOfDay' }
export const NYC_TIME_ZONE = 'America/New_York'
export const FE_SEPARATOR = '/'
export const BE_SEPARATOR = '-'
export const MASK_CHAR = 'X'
export const MASK = '99/99/9999'
export const PLACEHOLDER = 'MM/DD/YYYY'
const DATE_FORMAT = PLACEHOLDER

const isInvalidDate = date =>
  typeof date === 'string' && (date.includes(MASK_CHAR) || !moment(date, DATE_FORMAT).isValid())

const formatString = value => {
  if (!value || typeof value !== 'string') {
    return value
  }

  const [yyyy, mm, dd] = value.substr(0, 10).split(BE_SEPARATOR)

  return [mm, dd, yyyy].join(FE_SEPARATOR)
}

const normalizeToString = value => {
  if (!value || typeof value !== 'string') {
    return value
  }

  const [mm, dd, yyyy] = value.split(FE_SEPARATOR)
  const reversed = [yyyy, mm, dd].join(BE_SEPARATOR)

  return reversed === 'XXXX-XX-XX' ? '' : reversed
}

const formatTimestamp = value => {
  if (!value) {
    return ''
  }

  if (isInvalidDate(value)) {
    return value
  }

  return moment.tz(value, NYC_TIME_ZONE).format(DATE_FORMAT)
}

const normalizeToTimestamp = (value, subtype) => {
  if (isInvalidDate(value)) {
    return value
  }
  const parsed =
    subtype === DATEPICKER_SUBTYPES.END_OF_DAY
      ? moment(value, 'MM-DD-YYYY').endOf('day')
      : moment(value, 'MM-DD-YYYY').startOf('day')

  const result = switchTimezoneWithoutChangingValues(parsed).valueOf()

  return Number.isNaN(result) ? '' : result
}

export const fromCalendarToInputFormat = date => moment(date).format(DATE_FORMAT)

export const formatInitialMonth = value => {
  const date = new Date(value)

  if (DateUtils.isDate(date)) {
    return date
  }
}

export const HANDLERS_BY_TYPE = {
  string: {
    format: formatString,
    normalize: normalizeToString,
  },
  timestamp: {
    format: formatTimestamp,
    normalize: normalizeToTimestamp,
  },
}
