import React, { FC } from 'react'
import cx from 'classnames'

import Spinner from '../../assets/icons/loader.svg'
import iconStyles from '../../assets/icons/Icons.module.css'
import styles from './Loading.module.css'

interface LoadingProps {
  className?: string
  fullScreen?: boolean
  text?: React.ReactNode
  centered?: boolean
}

const Loading: FC<LoadingProps> = ({ className, fullScreen, text, centered }) => {
  const showText = !fullScreen && !centered
  return (
    <div
      className={cx({ [styles.fullScreen]: fullScreen, [styles.alignCenter]: centered }, className)}
    >
      <Spinner
        className={cx(styles.icon, iconStyles.icon, iconStyles.rotating, {
          [styles.textShown]: showText,
        })}
      />
      {showText && <span className={iconStyles.iconLabel}>{text || 'Loading...'}</span>}
    </div>
  )
}

export default Loading
