import React, { FC, useMemo } from 'react'
import cx from 'classnames'

import { useToggleState, useStopPropagation, useOnClickOutside } from '../../../hooks'
import Button from '../Button'
import Dropdown, { Item } from './Dropdown'

import styles from './ActionsDropdown.module.css'

type Actions = Array<boolean | Item>

interface ActionsDropdownProps {
  actions: Actions
  secondary?: boolean
  className?: string
  buttonClassName?: string
}

const areActionsInvalid = (actions: Actions): boolean =>
  !actions || !Array.isArray(actions) || !actions.length || actions.every(a => !a)

const ActionsDropdown: FC<ActionsDropdownProps> = ({
  className,
  buttonClassName,
  actions,
  secondary,
  ...restProps
}) => {
  const [isDisplayed, , hideDropdown, toggle] = useToggleState(false)
  const toggleDropdown = useStopPropagation(toggle)
  const ref = useOnClickOutside(hideDropdown)

  const actionsAreInvalid = areActionsInvalid(actions)

  const validActions = useMemo(() => actions.filter(Boolean), [actions])

  return (
    <div ref={ref} className={cx(styles.container, className)}>
      <Button
        onClick={toggleDropdown}
        disabled={actionsAreInvalid}
        kind={secondary ? 'secondary' : undefined}
        className={cx(styles.button, { [styles.secondary]: secondary }, buttonClassName)}
      >
        Actions
      </Button>

      {!actionsAreInvalid && (
        <Dropdown
          {...restProps}
          items={validActions}
          secondary={secondary}
          isDisplayed={isDisplayed}
          handleClose={hideDropdown}
        />
      )}
    </div>
  )
}

export default ActionsDropdown
