import { FormatReadonly, ReturnElement } from '../types'

export const renderReadonlyValue: FormatReadonly = (value, formatter) => {
  if (typeof formatter === 'function') {
    return formatter(value)
  }

  return value ? value : ''
}

export const renderEditableField = (field: ReturnElement, readonly: ReturnElement) => (
  isEditMode: boolean
): ReturnElement => (isEditMode ? field : readonly)
