import React, { FC } from 'react'
import cx from 'classnames'

import { Offset } from '../types'

import styles from './Row.module.css'

interface RowProps {
  className?: string
  full?: boolean
  width?: '100' | 'auto'
  noWrap?: boolean
  bounded?: boolean
  rtl?: boolean
  offset?: Offset
}

const Row: FC<RowProps> = ({
  className,
  full,
  width,
  noWrap,
  rtl,
  bounded,
  offset,
  children,
  ...restProps
}) => {
  const baseClassName = cx(
    styles.row,
    className,
    {
      [styles.full]: full,
      [styles.noWrap]: noWrap,
      [styles.rtl]: rtl,
      [styles.bounded]: bounded,
    },
    styles[`offset-${offset}`],
    styles[`width-${width}`]
  )

  return (
    <div className={baseClassName} {...restProps}>
      {children}
    </div>
  )
}

export default Row
