import React, { FC } from 'react'
import { connect as formikConnect } from 'formik'

import TitledBlock from '../../../elements/TitledBlock'
import { useEnhancedForm } from '../../utils'

interface FieldSetProps {
  label?: string | JSX.Element
  fieldClassName?: string
}

const FieldSet: FC<FieldSetProps> = ({ label, fieldClassName, children }) => {
  const { isEditMode } = useEnhancedForm()

  return (
    <TitledBlock
      label={label}
      className={fieldClassName}
      containerElement="fieldset"
      labelElement="legend"
      hidableLabel
    >
      {typeof children === 'function' ? children(isEditMode) : children}
    </TitledBlock>
  )
}

export default formikConnect(FieldSet)
