import * as Sentry from '@sentry/browser'

export default Sentry

const lastActions = []
const lastApiCalls = []

export const errorTrackingMiddleware = (isApiAction, isSimpleAction) => () => next => action => {
  const { type, payload } = action

  if (isSimpleAction(type)) {
    lastActions.unshift(type)
    lastActions.splice(10)
  }

  if (isApiAction(type)) {
    lastApiCalls.unshift(payload)
    lastApiCalls.splice(5)
  }

  return next(action)
}

export const actionsLogMiddleware = () => next => action => {
  Sentry.addBreadcrumb({
    category: 'redux-action',
    message: action.type,
    data: action,
  })

  return next(action)
}

export const getExtra = () => ({
  lastActions,
  lastApiCalls,
})

export const initializeErrorTracking = () => {
  const dsn = process.env.JUNO_SENTRY_DSN
  const environment = process.env.JUNO_ENV

  if (dsn && environment) {
    const sentryConfiguration = {
      dsn,
      environment,
      beforeSend: event => ({
        ...event,
        data: { ...event.data, ...getExtra() },
      }),
    }

    if (environment === 'production') {
      sentryConfiguration.release = __RELEASE_NAME__
    }

    Sentry.init(sentryConfiguration)
  }
}
