import React, { FC, ReactNode } from 'react'
import cx from 'classnames'

import styles from './TitledBlock.module.css'

const areChildrenPresent = (children: ReactNode): boolean => {
  const notEmptyChildren = Array.isArray(children) ? children.filter(Boolean) : children

  // React.Children.count function is right way to check count of visible children elements
  // e.g. React.Children.count([Array(0), Array(0), Array(0)]) === 0 -> true
  return Boolean(children) && React.Children.count(notEmptyChildren) > 0
}

interface TitledBlockProps {
  /** Label for block */
  label?: ReactNode
  /** Input name for label */
  name?: string
  /** Custom class name */
  className?: string
  /** Prop for removing bottom margin */
  noBottomMargin?: boolean
  /** Prop for hiding label if not presented */
  hidableLabel?: boolean
  /** Container Element */
  containerElement?: 'div' | 'fieldset'
  /** Label Element */
  labelElement?: 'label' | 'legend'
}

const TitledBlock: FC<TitledBlockProps> = ({
  className,
  label,
  children,
  noBottomMargin,
  hidableLabel,
  name,
  containerElement = 'div',
  labelElement = 'label',
}) => {
  const content = areChildrenPresent(children) ? children : ''

  const baseClassName = cx(
    styles.base,
    {
      [styles.noBottomMargin]: noBottomMargin,
    },
    className
  )

  const containerProps = { className: baseClassName }
  const labelProps = { htmlFor: name, className: styles.label }

  const showLabel = !hidableLabel || Boolean(label)

  return React.createElement(
    containerElement,
    containerProps,
    <React.Fragment>
      {showLabel && React.createElement(labelElement, labelProps, label)}
      <div className={styles.content}>{content}</div>
    </React.Fragment>
  )
}

export default TitledBlock
