import { humanizeKey } from './key2text'

const commonErrors = {
  'ERR_BAD_REQUEST': 'Validation has failed.',
  'ERR_INTERNAL': 'Something went wrong. Try again later.',
  'ERR_DEADLINE': 'Server too busy. Try again later.'
}

export const humanizeErrorCode = (code, customErrors = {}) => {
  return customErrors[code] || commonErrors[code] || humanizeKey(code.replace('ERR_', ''))
}

export const humanizeErrors = (err, customErrors) => {
  if (err.fields) {
    const fieldErrors = err.fields.reduce((errors, e) => {
      const fieldName = e.field_name.includes('unknown') ? '_error' : e.field_name
      errors[fieldName] = e.message
      return errors
    }, {})
    return fieldErrors
  }

  const code = err.code || 'ERR_INTERNAL'

  return {
    _error: humanizeErrorCode(code, customErrors)
  }
}
