import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'

import Notification from '../../../elements/Notification'
import { useAutofillState, omitNotFunctions, EnhancedFormContext } from '../../utils'
import styles from './InnerFormWithContext.module.css'

const InnerFormWithContext = ({
  submissionError,
  setSubmissionError,
  resetSubmissionError,
  isEditMode,
  setEditMode,
  setReadMode,
  onFormInit,
  linkFormActions,
  hideSubmissionError,
  formik,
  className,
  staticContainer,
  children,
}) => {
  const [autofilled, autofill, resetAutofill] = useAutofillState(formik)

  const contextValue = {
    submissionError,
    setSubmissionError,
    resetSubmissionError,
    isEditMode,
    setEditMode,
    setReadMode,
    autofilled,
    autofill,
    resetAutofill,
  }

  useEffect(() => {
    const actions = { formik: omitNotFunctions(formik), enhanced: omitNotFunctions(contextValue) }

    if (typeof onFormInit === 'function') {
      onFormInit(actions)
    }

    if (typeof linkFormActions === 'function') {
      linkFormActions(actions)
    }
  }, [])

  const containerStyles = cx(className, {
    [styles.container]: !staticContainer,
    [styles.loading]: formik.isSubmitting,
  })

  return (
    <div className={containerStyles}>
      <EnhancedFormContext.Provider value={contextValue}>
        {submissionError && !hideSubmissionError && (
          <Notification kind="danger">{submissionError}</Notification>
        )}
        {typeof children === 'function' ? children(formik, contextValue) : children}
      </EnhancedFormContext.Provider>
    </div>
  )
}

InnerFormWithContext.propTypes = {
  submissionError: PropTypes.string.isRequired,
  setSubmissionError: PropTypes.func.isRequired,
  resetSubmissionError: PropTypes.func.isRequired,
  isEditMode: PropTypes.bool.isRequired,
  setEditMode: PropTypes.func,
  setReadMode: PropTypes.func,
  onFormInit: PropTypes.func,
  linkFormActions: PropTypes.func,
  hideSubmissionError: PropTypes.bool,
  formik: PropTypes.object.isRequired,
  className: PropTypes.string,
  staticContainer: PropTypes.bool,
  children: PropTypes.oneOfType([PropTypes.node, PropTypes.func]),
}

export default InnerFormWithContext
