import React from 'react'
import cx from 'classnames'
import { send } from '@juno/shared/analytics'

export default class BackToTop extends React.Component {
  state = {
    onTop: window.pageYOffset < 300
  }

  componentDidMount() {
    this.intervalId = setInterval(this.intervalHandler, 500)
  }

  componentWillUnmount() {
    window.clearInterval(this.intervalId)
  }

  intervalHandler = () => {
    this.calculateTopPosition()
  }

  calculateTopPosition() {
    this.setState({ onTop: window.pageYOffset < 300 })
  }

  scrollToTop() {
    window.scrollTo(0, 0)
    send('event', {
      eventCategory: 'UI',
      eventAction: 'Click',
      eventLabel: 'Back to top'
    })
  }

  render() {
    const buttonClass = cx('back-to-top', { hide: this.state.onTop })
    return <div className={buttonClass} onClick={this.scrollToTop} />
  }
}
