import PropTypes from 'prop-types'
import React from 'react'
import cx from 'classnames'
import Sentry from '@juno/shared/error-tracking'

import Notification from '../elements/Notification'

/**
 * @deprecated Use Card instead
 */
class Group extends React.Component {
  static propTypes = {
    children: PropTypes.node,
    header: PropTypes.node,
    className: PropTypes.string,
  }

  state = {
    error: null,
  }

  componentDidCatch(error, errorInfo) {
    this.setState({ error })
    console.warn(`Caught the following error in "${this.props.header}" GROUP:\n`, error) // eslint-disable-line no-console
    console.warn('Additional error info:', errorInfo) // eslint-disable-line no-console
    Sentry.captureException(error, { extra: errorInfo })
  }

  render() {
    const { error } = this.state
    const { className, header, children, ...restProps } = this.props

    return (
      <div className={cx('card', className)} {...restProps}>
        {header && <h3 className="title-common card__title">{header}</h3>}
        {error ? <Notification kind="danger">Something went wrong</Notification> : children}
      </div>
    )
  }
}

export default Group
