import React, { Component } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import { Group, GroupHeader } from '../index'

class GroupWithTabs extends Component {
  static propTypes = {
    children: PropTypes.node.isRequired,
    label: PropTypes.string,
    options: PropTypes.object.isRequired
  }

  state = {
    active: Object.keys(this.props.options)[0]
  }

  render() {
    const { children, label, options } = this.props
    const { active } = this.state

    return (
      <Group>
        <GroupHeader label={label}>
          <div className="filter-group">
            {Object.keys(options).map(option => (
              <span
                key={option}
                className={cx('filter-group__item', { active: option === active })}
                onClick={() => {
                  this.setState({
                    active: option
                  })
                }}
              >
                {option}
              </span>
            ))}
          </div>
        </GroupHeader>

        {children(options[active])}
      </Group>
    )
  }
}

export default GroupWithTabs
