import React, { FC } from 'react'
import { useField } from 'formik'

import { GenericInput, FieldBlock } from '../../'
import { renderReadonlyValue } from '../../utils'
import { Field, InputGeneric, ReturnElement } from '../../types'

const Input: FC<Field & InputGeneric> = ({
  name,
  label,
  fieldClassName,
  formatReadonlyValue,
  inputClassName,
  validate,
  disabled,
  ...restProps
}) => {
  const [field] = useField({ name, validate })

  return (
    <FieldBlock label={label} fieldClassName={fieldClassName} name={name}>
      {(isEditMode: boolean): ReturnElement =>
        isEditMode && !disabled ? (
          <GenericInput
            field={field}
            className={inputClassName}
            disabled={disabled}
            {...restProps}
          />
        ) : (
          renderReadonlyValue(field.value, formatReadonlyValue)
        )
      }
    </FieldBlock>
  )
}

export default Input
