import React, { useCallback } from 'react'
import PropTypes from 'prop-types'

import Form from './Form'
import { FormModeToggle } from './components'
import { CollapsibleCard } from '../../'
import { useToggleState } from '../../../hooks'
import { FORM_MODES } from '../utils'

const EditableCard = ({
  header,
  container,
  cardProps,
  isEditable,
  submitActionName,
  pendingActionName,
  actions,
  className,
  children,
  initialMode,
  disableEdit,
  onEditMode,
  onReadMode,
  formToggleClassName,
  ...restProps
}) => {
  const [isEditMode, setEditMode, setReadMode] = useToggleState(initialMode === FORM_MODES.EDIT)

  const setEdit = useCallback(() => {
    setEditMode()
    typeof onEditMode === 'function' && onEditMode()
  }, [onEditMode, setEditMode])

  const setRead = useCallback(() => {
    setReadMode()
    typeof onReadMode === 'function' && onReadMode()
  }, [onReadMode, setReadMode])

  const containerProps =
    typeof container === 'string'
      ? { className }
      : { noBottomPadding: true, ...cardProps, header, className }

  return React.createElement(
    container || CollapsibleCard,
    containerProps,
    <Form
      enableReinitialize
      {...restProps}
      isEditMode={isEditMode}
      setEditMode={setEdit}
      setReadMode={setRead}
      staticContainer
    >
      {(formik, context) => (
        <>
          {isEditable && (
            <FormModeToggle
              submitActionName={submitActionName}
              pendingActionName={pendingActionName}
              isEditMode={isEditMode}
              setEditMode={setEdit}
              setReadMode={setRead}
              actions={actions}
              disableEdit={disableEdit}
              formToggleClassName={formToggleClassName}
            />
          )}
          {typeof children === 'function' ? children(formik, context) : children}
        </>
      )}
    </Form>
  )
}

EditableCard.defaultProps = {
  initialMode: FORM_MODES.READ,
  isEditable: true,
  cardProps: {},
}

EditableCard.propTypes = {
  /** Initial values of the form (formik API) */
  initialValues: PropTypes.object.isRequired,
  /**
   * On submit form handler.
   *
   * The following object will be passed into:
   * {
   *    values,           - form values formatted by `formatValuesBeforeSubmission` (see prop below)
   *    initialValues,    - initial values for form (useful for comparing with `values`)
   *    __files__,        - files, that are spread out of values before
   *    formikActions,    - formik actions (useful for setting errors manually)
   *    enhancedActions   - enhanced actions (useful for setting submission error)
   * }
   */
  onSubmit: PropTypes.func.isRequired,
  /**
   * Card text header (strings only to support valid GA).
   */
  header: PropTypes.string,
  /**
   * Use this prop to pass enhanced containers (e.g. Collapsible Card with GA).
   */
  containersMap: PropTypes.object,
  /**
   * Outer container
   */
  container: PropTypes.elementType,
  /**
   * Additional props passed directly to the container
   */
  cardProps: PropTypes.object,
  /**
   * Initial form mode.
   * For <Form> component default mode is `edit`.
   * For <EditableCard> component (this one) default mode is `read`.
   *
   * Common use-case - render <EditableCard> with `edit` mode by default.
   */
  initialMode: PropTypes.oneOf([FORM_MODES.READ, FORM_MODES.EDIT]),
  /**
   * Class name that will be passed to <Group> component.
   */
  className: PropTypes.string,
  /**
   * Submit button action name, default is "Save". Will be passed to <FormModeToggle>.
   *
   * Common use-case - re-word submit to `Send`.
   */
  submitActionName: PropTypes.string,
  /**
   * Submit button action name, when submit promise is in `pending` status.
   * Default is "Saving...". Will be passed to <FormModeToggle>.
   *
   * Common use-case - re-word submit to `Sending...`.
   */
  pendingActionName: PropTypes.string,
  /**
   * Array of actions, in case you want to display "Actions dropdown" instead of usual "Edit button.
   */
  actions: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      onClick: PropTypes.func.isRequired,
    })
  ),
  /**
   * Value controls if <FormModeToggle> will be rendered. Default is `true`.
   *
   * Common use-case - forbid ability to edit if user has no permissions.
   */
  isEditable: PropTypes.bool,
  /** Content of the form */

  /**
   * Formats values when form submission is triggered but before calling `onSubmit` from props.
   * The result of this func will be passed as `values` to `onSubmit` handler.
   * Two arguments are passed to this func: `values`, `initialValues`.
   *
   * Common use-case - compute extra value depending on others, e.g. in car registration document .
   */
  formatValuesBeforeSubmission: PropTypes.func,
  /**
   * Hides default form submission error.
   * Default submission error is controlled by EnhancedForm context
   * and located at the top inside form (before children).
   *
   * Common use-case - you want to show submission error in custom place.
   */
  hideSubmissionError: PropTypes.bool,
  /**
   * Map that is used for humanazing submission errors.
   * Common errors will be humanized out-of-box.
   * Example: { ERR_SOME_ERROR: 'Some error normal text' }.
   */
  errorMap: PropTypes.object,
  /**
   * Function, that will be executed on form initialize phase.
   * Both formik and enhanced actions will be passed
   * to this func - ({ formik: { ...actions }, enhanced: {...actions} }).
   *
   * Common use-case - attach `onDMVScanComplete` to window, to handle autofill.
   */
  onFormInit: PropTypes.func,
  /**
   * In rare cases, where you want to lift both formik and enhanced actions up
   * ({ formik: { ...actions }, enhanced: {...actions} }), you can pass this func,
   * which should save link to formActions object to state. As an example, you could
   * take a look how this component saves actions via <FormInitializer> component,
   * to make them accessible in state for autofill implementation.
   *
   * Usually, you should use <WithForm>, <WithFormik> or <WithEnhancedForm> utility components,
   * which will pass necessary form actions as render-props inside form.
   */
  linkFormActions: PropTypes.func,
  /**
   * If form container should have position static.
   */
  staticContainer: PropTypes.bool,
  /**
   * All rest props are considered as formik props and will be passed into <Formik> component.
   */
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.node]),
  disableEdit: PropTypes.bool,
  onEditMode: PropTypes.func,
  onReadMode: PropTypes.func,
  formToggleClassName: PropTypes.string,
  /**
   * All other props will be passed to <Form>.
   * They can be considered as two following groups:
   *  - Props, that will be used directly by <Form> to patch its functionality.
   *    Take a look at <Form>'s props documentation for additional details.
   *  - Props, that will be passed by <Form> to <Formik> component.
   *    Take a look at https://jaredpalmer.com/formik/docs/api/formik for additional details.
   */
}

export default EditableCard
