import PropTypes from 'prop-types'
import React from 'react'
import cx from 'classnames'

const Notification = ({ className, children, inline, kind, onClose }) => (
  <div className={cx('notification', className, kind, { inline })}>
    {onClose && (
      <span className="notification__close" onClick={onClose}>
        <span>&times;</span>
      </span>
    )}
    {children}
  </div>
)

Notification.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  inline: PropTypes.bool,
  kind: PropTypes.oneOf(['success', 'warning', 'danger']),
  onClose: PropTypes.func,
}

export default Notification
