import React, { useRef, useState, useEffect, FC } from 'react'
import cx from 'classnames'

import styles from './Dropdown.module.css'

const BREAKPOINT_TO_CHANGE_DROPDOWN_DIRECTION = 300

export interface Item {
  name: string
  onClick: () => void
}

interface DropdownProps {
  isDisplayed: boolean
  handleClose: (e: React.MouseEvent) => void
  items: Array<Item>
  secondary?: boolean
  staticSide?: 'left' | 'right'
  className?: string
}

const Dropdown: FC<DropdownProps> = ({
  isDisplayed,
  handleClose,
  items,
  secondary,
  staticSide,
  className,
}) => {
  const dropdown = useRef<HTMLInputElement>(null)
  const [isOutbound, setIfOutbound] = useState(false)

  useEffect(() => {
    if (staticSide || !isDisplayed) {
      return
    }

    const left = dropdown && dropdown.current ? dropdown.current.getBoundingClientRect().left : 0

    const isDropdownOutbound = window.innerWidth - left < BREAKPOINT_TO_CHANGE_DROPDOWN_DIRECTION
    setIfOutbound(isDropdownOutbound)
  }, [staticSide, isDisplayed])

  const containerClassNames = cx(
    styles.container,
    staticSide && styles[staticSide],
    { [styles.show]: isDisplayed, [styles.outbound]: isOutbound, [styles.withShadow]: secondary },
    className
  )

  return (
    <div ref={dropdown} onClick={handleClose} className={containerClassNames}>
      {items.map(({ name, onClick }) => (
        <div
          key={name}
          onClick={event => {
            event.stopPropagation()
            onClick()
          }}
          className={cx(styles.item, { [styles.secondary]: secondary })}
        >
          {name}
        </div>
      ))}
    </div>
  )
}

export default Dropdown
