import moment from 'moment-timezone'

export const UTCtoTZ = (value, tz) => {
  tz = 'America/New_York' // Fallback to America/New_York timezone for now
  let utc = moment.utc(value)
  return utc.tz(tz)
}

export const switchTimezoneWithoutChangingValues = (dateTime, timezone = 'America/New_York') => {
  const another = dateTime.clone().tz(timezone)
  // shift the moment by the difference in offsets
  another.add(dateTime.utcOffset() - another.utcOffset(), 'minutes')
  return another
}
