import React, { useCallback, FC } from 'react'
import cx from 'classnames'

import styles from './Button.module.css'

type ButtonType = 'button' | 'submit' | 'reset'

interface ButtonProps {
  className?: string
  disabled?: boolean
  kind?: 'success' | 'alert' | 'warning' | 'secondary'
  small?: boolean
  fullWidth?: boolean
  flat?: boolean
  onClick?: (e: React.MouseEvent) => void
  minWidth?: boolean
  type?: ButtonType
}

const Button: FC<ButtonProps> = ({
  className,
  children,
  disabled,
  flat,
  kind,
  onClick,
  small,
  fullWidth,
  minWidth,
  type,
  ...props
}) => {
  const handleClick = useCallback(
    e => {
      e.preventDefault()
      onClick && onClick(e)
    },
    [onClick]
  )

  return (
    <button
      className={cx(styles.button, styles[kind || 'default'], className, {
        [styles.minWidth]: minWidth,
        [styles.fullWidth]: fullWidth,
        [styles.disabled]: disabled,
        [styles.small]: small,
        [styles.flat]: flat,
      })}
      disabled={disabled}
      onClick={onClick && handleClick}
      type={type}
      {...props}
    >
      {children}
    </button>
  )
}

export default Button
