import React, { FC } from 'react'
import { useField, useFormikContext } from 'formik'

import { ErrorMessage, FieldSet, GenericCheckbox } from '../../'
import { useFormattedOptions, getErrorMessage } from '../../utils'
import { CustomChangeHandler, Field, FormValues, Options, ReturnElement } from '../../types'
import styles from './CheckboxesGroup.module.css'

interface CheckboxesGroupProps {
  options: Options
  disabledList?: string[]
  disabled?: boolean
  errorClassName?: string
  /** Custom function that will be executed on change (new value and setValue will be passed in) */
  onChangeCustom?: CustomChangeHandler
}

const CheckboxesGroup: FC<Field & CheckboxesGroupProps> = ({
  name,
  options,
  label,
  disabledList = [],
  disabled,
  fieldClassName,
  errorClassName,
  inputClassName,
  validate,
  ...restProps
}) => {
  const form = useFormikContext<FormValues>()

  // TODO: Remove after https://github.com/jaredpalmer/formik/issues/1961 resolved
  // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
  // @ts-ignore
  const [field] = useField<string[]>({ name, validate })

  const errorMessage = getErrorMessage(field.name, form)
  const formattedOptions = useFormattedOptions(options)

  return (
    <FieldSet label={label} fieldClassName={fieldClassName}>
      {(isEditMode: boolean): ReturnElement => (
        <React.Fragment>
          <div className={styles.checkboxesGroup}>
            {formattedOptions.map(([key, description]) => (
              <GenericCheckbox
                field={field}
                disabled={!isEditMode || disabled || disabledList.includes(key)}
                itemKey={key}
                itemDescription={description}
                className={inputClassName}
                key={key}
                {...restProps}
              />
            ))}
          </div>
          {isEditMode && (
            <ErrorMessage errorMessage={errorMessage} errorClassName={errorClassName} />
          )}
        </React.Fragment>
      )}
    </FieldSet>
  )
}

export default CheckboxesGroup
