import React, { useCallback, FC } from 'react'
import { FieldInputProps, useFormikContext } from 'formik'
import cx from 'classnames'

import { getErrorMessage } from '../../utils'
import { BasicGeneric, ChangeHandler, FormValues, GroupGeneric } from '../../types'

import styles from './GenericCheckbox.module.css'

export interface GenericCheckboxProps extends GroupGeneric {
  field: FieldInputProps<string[]>
  /** Key of the checkbox item - will be passed to value[] */
  itemKey: string
  /** Name of the checkbox item - will be shown to an user */
  itemDescription: string
}

/*
  This component should be used only for array add/delete cases.
  In case you want to toggle one value to true/false - use GenericToggle instead.
*/
const GenericCheckbox: FC<BasicGeneric & GenericCheckboxProps> = ({
  field,
  itemKey,
  itemDescription,
  className,
  disabled,
  onChangeCustom,
}) => {
  const form = useFormikContext<FormValues>()

  const { value, name } = field
  const errorMessage = getErrorMessage(name, form)

  const isThisValueChecked = Array.isArray(value) && value.includes(itemKey)
  const checkboxItemClass = cx(styles.item, className, { [styles.disabled]: disabled })

  // Removes item if it's present in an array and add if absent
  const handleChange = useCallback(() => {
    const nextValue = value ? [...value] : []
    const index = Array.from(value).indexOf(itemKey)
    const setValue: ChangeHandler = val => form.setFieldValue(name, val)

    if (index > -1) {
      nextValue.splice(index, 1)
    } else {
      nextValue.push(itemKey)
    }

    if (typeof onChangeCustom === 'function') {
      onChangeCustom(nextValue, setValue)
      return
    }

    setValue(nextValue)
  }, [value])

  const inputContainerClassName = cx(styles.inputContainer, {
    [styles.checked]: isThisValueChecked,
  })

  return (
    <label className={checkboxItemClass}>
      <div className={inputContainerClassName}>
        <input
          {...field}
          id={name}
          onChange={handleChange}
          checked={isThisValueChecked}
          disabled={disabled}
          value={undefined}
          type="checkbox"
          className={styles.input}
        />
      </div>
      <span className={cx(styles.label, { [styles.invalid]: Boolean(errorMessage) })}>
        {itemDescription}
      </span>
    </label>
  )
}

export default GenericCheckbox
