import { useContext, useEffect, useState, useCallback, useMemo } from 'react'
import { get } from 'lodash-es'
import dot from 'dot-prop-immutable'

import { EnhancedFormContext, formatOptions } from './'

/*
  Returns, depending on if "fieldName" param is present:
    - If missing: enhanced form context
    - If present: enhanced form context + if this particular field was autofilled
*/
export const useEnhancedForm = fieldName => {
  const context = useContext(EnhancedFormContext)

  if (!fieldName) {
    return context
  }

  const isAutofilled = get(context, `autofilled.${fieldName}`)

  return { ...context, isAutofilled }
}

const SECONDS_AUTOFILL_ANIMATION_PLAYS = 2000

/*
  This hook is needed for cleanup autofilled data, to make it possible to
  apply autofilled className once again on the second and further autofills.
*/
export const useClearAutofill = (isAutofilled, resetAutofill) => {
  useEffect(() => {
    if (isAutofilled) {
      setTimeout(() => {
        resetAutofill()
      }, SECONDS_AUTOFILL_ANIMATION_PLAYS)
    }
  }, [isAutofilled])
}

export const useFormattedOptions = options => {
  return useMemo(() => formatOptions(options), [options])
}

export const useSubmissionErrorState = () => {
  const [submissionError, setSubmissionError] = useState('')

  const resetSubmissionError = useCallback(() => {
    setSubmissionError('')
  }, [setSubmissionError])

  return [submissionError, setSubmissionError, resetSubmissionError]
}

export const useAutofillState = formik => {
  const [autofilled, setAutofilledField] = useState({})

  const autofill = useCallback(
    (fieldName, value) => {
      setAutofilledField(prev => dot.set(prev, fieldName, true))
      formik.setFieldValue(fieldName, value)
    },
    [setAutofilledField]
  )

  const resetAutofill = useCallback(
    fieldName => {
      setAutofilledField(prev => dot.delete(prev, fieldName))
    },
    [setAutofilledField]
  )

  return [autofilled, autofill, resetAutofill]
}
