import React, { Component } from 'react'
import cx from 'classnames'

import Button from '../../elements/Button'
import Spinner from '../../assets/icons/loader.svg'
import SuccessIcon from '../../assets/icons/success.svg'
import ErrorIcon from '../../assets/icons/danger.svg'
import iconStyles from '../../assets/icons/Icons.module.css'
import styles from './ActionButton.module.css'

interface ActionButtonProps {
  className?: string
  children: React.ReactNode
  disabled?: boolean
  onClick: () => Promise<unknown>
  small?: boolean
}

type status = 'pending' | 'success' | 'error' | null

interface ActionButtonState {
  status: status
}

class ActionButton extends Component<ActionButtonProps, ActionButtonState> {
  state = {
    status: null,
  }

  timeout?: number = undefined
  unmounted = false

  componentWillUnmount() {
    this.timeout && clearTimeout(this.timeout)
    this.unmounted = true
  }

  setStatus = (status: status) => {
    if (this.unmounted) return
    this.setState({ status })
    this.timeout = window.setTimeout(this.clearButtonState, 2500)
  }

  clearButtonState = () => {
    this.setState({ status: null })
  }

  handleClick = () => {
    window.clearTimeout(this.timeout)
    this.setState({ status: 'pending' })
    this.props
      .onClick()
      .then(() => this.setStatus('success'))
      .catch(() => this.setStatus('error'))
  }

  render() {
    const { status } = this.state
    const { children, className, disabled, small, ...restProps } = this.props

    return (
      <Button
        {...restProps}
        className={cx(styles.actionButton, styles[status || ''], className)}
        disabled={disabled || status === 'pending'}
        onClick={this.handleClick}
        small={small}
      >
        <span className={styles.text}>{children}</span>
        <Spinner className={cx(iconStyles.rotating, styles.icon, styles.iconPending)} />
        <SuccessIcon className={cx(styles.icon, styles.iconSuccess)} />
        <ErrorIcon className={cx(styles.icon, styles.iconError)} />
      </Button>
    )
  }
}

export default ActionButton
