import React, { FC } from 'react'
import moment from 'moment-timezone'

interface DateTimeProps {
  className?: string
  date?: string | number | moment.Moment
  timezone?: string
  format?: string
}

const DatetimeTZ: FC<DateTimeProps> = ({
  className,
  date,
  timezone = 'America/New_York',
  format = 'lll',
}) => {
  if (!date) {
    return null
  }

  return (
    <div className={className}>
      {moment
        .utc(date)
        .tz(timezone)
        .format(format)}
    </div>
  )
}

export default DatetimeTZ
