import PropTypes from 'prop-types'
import React from 'react'
import Loading from './Loading'
import ApiFailed from './ApiFailed'

const ApiActionStatus = ({ className, children, state, hideLoading, hideFailed }) => {
  if (state.status === 'pending' && !hideLoading) {
    return <Loading className={className} />
  } else if (state.status === 'failed' && !hideFailed) {
    return <ApiFailed className={className} errors={state.errors} />
  } else if (state.status === 'success') {
    return children || null
  }
  return null
}

ApiActionStatus.propTypes = {
  state: PropTypes.object.isRequired,
}

export default ApiActionStatus
