import { useEffect, useRef, useState, useCallback } from 'react'

export const useSkipFirstRenderWithTimeout = (milliseconds = 0) => {
  /*
    We use ref hook here instead of state, because ref hook can be used as class instance fields,
    and isMounted is usually stored as instance field in case class components.
  */
  const isFirstRenderSkipped = useRef(false)

  useEffect(() => {
    setTimeout(() => {
      isFirstRenderSkipped.current = true
    }, milliseconds)
  }, [])

  return isFirstRenderSkipped.current
}

export const useToggleState = initialState => {
  const [value, setValue] = useState(initialState)

  const setToTrue = useCallback(() => setValue(true), [setValue])
  const setToFalse = useCallback(() => setValue(false), [setValue])
  const toggle = useCallback(() => setValue(prev => !prev), [setValue])

  return [value, setToTrue, setToFalse, toggle]
}
